import Vue from 'vue';
import Router from 'vue-router';

import HomePage from './modules/home/HomePage'
import LoginPage from './modules/login/LoginPage'
import RegisterPage from './modules/register/RegisterPage'
import PropertyListing from './modules/property/PropertyListing'
import PropertyEdit from './modules/property/PropertyEdit'
import Teste from './modules/test/Test'
import Applications from './modules/user/Applications'

import { routes as UserRoutes } from './modules/user'

Vue.use(Router);

export default [
    // { path: '/', component: Teste },
    { path: '/', component: PropertyEdit, name:"home" },
    { path: '/login', component: LoginPage },
    { path: '/test', component: Teste },
    { path: '/applications', component: Applications },
    // { path: '/property/listing', component: PropertyListing },
    // { path: '/property/edit', component: PropertyEdit },

    ...UserRoutes,

    // otherwise redirect to home
    { path: '*', redirect: '/' }
]


