<script>
import { Methods } from "vue-handler-panels";
import { Gallery } from "@/lib/vue-file-upload";
import _ from "lodash";

export default {
  props: {
    uid: {
      type: String
    }
  },
  data() {
    return {
      data: null,
      dirty: false,
      $ct: null
    };
  },
  methods: {
    ...Methods,
    initialize() {
      //
      this.$parent_vm = this.$ct.prev().$vm;

      this.data = this.$parent_vm.data;

      if (!_.has(this.data, "config")) {
        this.$set(this.data, "config", {});
      }

      if (!_.has(this.data.config, "media")) {
        this.$set(this.data.config, "media", {});
      }

      // if (!_.has(this.data.config.media, "photos")) {
      //   this.$set(this.data.config.media, "photos", {
      //     app_id: "p8EMQEJk",
      //     bucket: "ppt-R3w0p8Vw",
      //     service_url: "https://pictureimobland.blob.core.windows.net",
      //     picture_src: "/gallery/{app}/{bucket}/{id}/{id}-{size}.jpg"
      //   });
      // }
    },
    getData() {
      // console.log("editor get data");
      return this.data;
    },
    btnClose() {
      this.close();
    },
    findFiles() {
      this.$refs.gallery.findFiles();
    },
    closeConfirm() {
      if (this.sending()) {
        alert("As fotos ainda esto sendo processadas");
        return false;
      }
      return  true;
    },
    sending() {
      return this.$refs.gallery.sending();
    }
  },
  created() {
    this.$ct = this.$parent.openedPanels[this.uid];
    this.initialize();
  },
  components: {
    Gallery
  }
};
</script>

<template>
  <div class="panel-component">
    <div class="panel">
      <div class="panel-heading">
        <i data-v-176308fc class="far fa-images"></i>
        <div class="title">Fotos</div>
        <button class="btn btn-sm btn-default" @click="btnClose()">
          <i class="fas fa-times"></i>
        </button>
      </div>
      <Gallery v-if="data" ref="gallery" :data="data" :config="data.config.media.photos"/>
      <div class="panel-body" v-if="!data">Sem dados</div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.panel-component {
  flex-direction: column;

  .panel {
    width: 622px !important;

    .panel-heading {
      border-bottom: 1px solid #e1e5ea;

      .btn-export {
        margin-right: 6px;
        font-size: 13px;
        outline: none;
        box-shadow: none;
        background: #fff;
        border: 1px solid #ddd;

        i {
          font-size: 13px;
        }

        &.active {
          border: 1px solid #eee;
          font-weight: 500;
        }
      }
    }
  }
}
</style>



