<script>
import { mapState, mapActions } from "vuex";

export default {
  computed: {
    ...mapState({
      auth: state => state.auth,
      application: state => state.auth.application
    })
  }
};
</script>
<template>
  <div class="sidebar" v-if="application">
    <div class="page-sidebar-profile">
      <div class="main-pic">
        <a class="pic" href="#" style target="_blank"></a>
      </div>
      <div class="profile-name">
        <a href="#" target="_blank">{{application.name}}</a>
      </div>
    </div>
    <ul class="sidebar-menu">
      <li class="start open">
        <ul class="sub-menu" style="display:block">
          <li class="active">
            <router-link to="/">
              <i class="fas fa-home"></i>
              <div class="label">Imveis</div>
              <!-- <span class="badge">000</span> -->
            </router-link>
          </li>
          <!-- <li class>
            <router-link to="/client/listing">
              <i class="far fa-user"></i>
              <div class="label">Clientes</div>
              <span class="badge">0</span>
            </router-link>
          </li>
          <li class>
            <router-link to="/agent/listing">
              <i class="fas fa-user"></i>
              <div class="label">Corretores</div>
              <span class="badge">10</span>
            </router-link>
          </li>
          <li class>
            <router-link to="/realestate/edit">
              <i class="fas fa-home"></i>
              <div class="label">A imobiliria</div>
            </router-link>
          </li>
          <li class>
            <router-link to="/realestate/edit">
              <i class="fas fa-user-cog"></i>
              <div class="label">Minha Conta</div>
            </router-link>
          </li>
          <li class>
            <router-link to="/realestate/edit">
              <i class="fas fa-bolt"></i>
              <div class="label">Registro de Atividades</div>
            </router-link>
          </li>-->
        </ul>
      </li>
    </ul>
  </div>
</template>

<style lang="scss">
.sidebar {
  .page-sidebar-profile {
    padding: 15px 15px;
    background: rgba(0, 0, 0, 0.2);
    display: flex;
    align-items: center;
    .profile-name {
      padding: 0px;
      a {
        color: #fff;
        font-size: 12px;
        text-transform: uppercase;
      }
    }
    .main-pic {
      margin-right: 10px;
      a.pic {
        display: block;
        width: 50px;
        height: 50px;
        background-position: center;
        background-repeat: no-repeat;
        background-color: #fff;
        border-radius: 100px;
        background-size: 100%;
      }
    }
  }
  ul.sidebar-menu {
    font-family: roboto;
    ul.sub-menu {
      margin-top: 10px;

      & > li > a {
        text-decoration: none;
        color: #fff;
        padding: 10px 15px;
        height: 40px;
        display: flex;
        align-items: center;
        border-bottom: 1px solid rgba(0, 0, 0, 0);
        transition: all 0.2s ease;

        i {
          width: 20px;
          font-size: 13px;
          color: rgba(255, 255, 255, 0.5);
        }

        .label {
          flex-grow: 1;
          text-align: left;
          font-size: 15px;
          font-weight: 300;
        }

        .badge {
          font-size: 12px;
          font-weight: normal;
          padding: 5px 10px;
          border-radius: 5px;
          background: rgba(0, 0, 0, 0.68);
          transition: all 0.2s ease;
          margin: -8px 0 -6px;
        }

        &:hover,
        &.active {
          background: #1c3d79 !important;
          border-bottom: 1px solid rgba(0, 0, 0, 0);

          .badge {
            background: rgba(39, 121, 255, 0.62);
          }
        }
      }
    }
  }
}
</style>



