var env = process.env;

var apiUrl = 'http://api.net';
var domain_accounts = 'http://accounts.net';
var domain = 'admin-vue.net';

// -------------------------------------------------------

if (env.NODE_ENV == 'production') {

    apiUrl = 'https://api-imobland.azurewebsites.net';

    domain = 'admin-imobland.github.io';

    domain_accounts = 'https://accounts-imobland.azurewebsites.net';
}

const config = {
    env: env.NODE_ENV,
    apiUrl,
    domain,
    domain_accounts
}

export default config;


