<script>
// import JsonVersioner from "@/lib/json-versioner";
import JsonVersioner from "json-versioner";

export default {
  data() {
    return {
      pattern: {
        "--lock": ["alpha"],
        alpha: {
          name: "ALPHA"
        }
      },
      request_data: {
        "--hide": ["alpha"],
        alpha: {
          name: "OK"
        }
      },
      current_data: {},
      data: {},
      changes: {},
      diff: {},
      nextmix: {},
      nextmix_diff: {}
    };
  },
  created() {

    var $jsv = new JsonVersioner();

    this.current_data = $jsv.merge(this.pattern, this.request_data);

    this.data = $jsv.clone(this.current_data);

    this.data["--teste"] = true;

    // this.changes = $jsv.diff(this.current_data, this.data);

    this.diff = $jsv.diff(this.pattern, this.data);

    this.nextmix = $jsv.merge(this.pattern, this.diff);

    this.nextmix_diff = $jsv.diff(this.pattern, this.nextmix);

    if (JSON.stringify(this.nextmix_diff) != JSON.stringify(this.diff)) {
      alert("Nao bateu");
    }
  }
};
</script>

<template>
  <div class="scope">
    <div class="panel">
      <div class="panel-heading">Pattern</div>
      <div class="panel-body">{{pattern}}</div>
    </div>
    <div class="panel">
      <div class="panel-heading">Request data</div>
      <div class="panel-body">{{request_data}}</div>
    </div>
    <div class="panel">
      <div class="panel-heading">Current data</div>
      <div class="panel-body">{{current_data}}</div>
    </div>
    <div class="panel">
      <div class="panel-heading">Data</div>
      <div class="panel-body">{{data}}</div>
    </div>
    <!-- <div class="panel">
      <div class="panel-heading">Changes</div>
      <div class="panel-body">{{changes}}</div>
    </div>-->
    <div class="panel">
      <div class="panel-heading">* DIFF - OUTPUT</div>
      <div class="panel-body">{{diff}}</div>
    </div>
    <div class="panel">
      <div class="panel-heading">Next Merge</div>
      <div class="panel-body">{{nextmix}}</div>
    </div>
    <div class="panel">
      <div class="panel-heading">* Next Merge - DIFF</div>
      <div class="panel-body">{{nextmix_diff}}</div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.scope {
  padding: 15px 30px;
  .panel {
    border: none;
    border-radius: 2px;

    .panel-heading {
      padding: 2px 10px;
      border-bottom: 1px solid #eceded;
      background: #f8f9fa;
    }
    .panel-body {
      padding: 5px 10px;
    }
  }
}
</style>


