<script>
import { mapState, mapActions } from "vuex";
import { propertyService } from "@/services";
var $ = require("jquery");

import { Handler } from "@/vendor/vue-handler-panels";

import MainPanel from "./Main";

import PropertyListing from "./Listing";

export default {
  data() {
    return {
      id: this.$route.query.id,
      property: null
    };
  },
  computed: {
    ...mapState({
      auth: state => state.auth,
      application: state => state.auth.application
    })
  },
  created() {
    // this.getByID(this.id);
  },
  mounted() {
    this.handler = this.$refs.handler;

    this.handler.registerPanel("main", MainPanel);
    this.handler.registerPanel("listing", PropertyListing);

    this.handler.openNextPanel("listing", $vm => {
      // $vm.setData({
      //   value: "ALPHA"
      // });
    });

    this.handler.$on("hook:updated", x => {
      this.autoScroll();
    });
  },
  methods: {
    setAuthApi(id) {
      window.location.href = "/#/?auth_application_id=" + id;
    },
    ...mapActions("properties", {
      //
    }),
    onClick(property) {
      this.$router.push("/#/property/edit?id=" + property.id);
    },
    getByID(id) {
      propertyService
        .getById(id)
        .then(({ data }) => {
          this.property = data.data;
        })
        .catch(x => {
          // console.log(x)
        });
    },

    autoScroll() {
      this.$el.scrollLeft = 10000;
    }
  },
  components: {
    Handler
  }
};
</script>

<template>
  <div class="bg-panels">
    <Handler ref="handler"/>
  </div>
</template>

<style lang="scss">
div.bg-panels {
  // background: #f6f8f9;
  // background: #e6e8ec;
  background: #f1f3f5;
  width: 100%;
  display: flex;
  scroll-behavior: smooth;
  overflow-x: auto;
  padding: 0px 20px;

  .vue-handler-panels {
    border-right: 1px solid #d8d8d8;
    border-left: 1px solid #d8d8d8;
    box-shadow: 2px 0px 8px -4px rgba(0, 0, 0, 0.2);

    .btn {
      border-radius: 2px;
    }
    .form-control {
      border-color: #d8d8d8;
      border-radius: 1px !important;
    }

    .panel-component {
      &:not(:first-child) {
        border-left: 1px solid #e5e7ea;
      }
      .panel {
        height: 100%;
        border-radius: 0px;
        border: none;
        box-shadow: none;

        .panel-heading {
          height: 50px;
          border: none;
          padding: 0px;
          border-bottom: 1px solid #e5e7ea;
          color: #545a5f;
          padding-left: 15px;
          
          .title {
            line-height: 15px;
          }
          .btn {
            height: 100%;
            border: 0px;
            margin: 0px !important;
            font-size: 15px;
            i {
              width: 20px;
            }
          }
          & > i:first-child {
            font-size: 16px;
            margin-right: 10px;
          }
        }
        h1.title {
          margin-left: 0px;
          margin-top: 0px;
          color: #4e5052;
          font-size: 19px;
          font-weight: 400;
          font-family: roboto;
        }
      }
    }
  }
}

#page-home {
  ul.properties {
    display: flex;
    flex-direction: column;
    border-top: 1px solid #e4e5e6;

    & > li {
      padding: 10px;

      border-bottom: 1px solid #e4e5e6;
      background: #fafafb;
      padding: 10px;
      display: flex;

      & > .img {
        height: 50px;
        width: 50px;
        background: #fff;
        border: 1px solid #eee;

        display: flex;
        justify-content: center;
        align-items: center;

        .fas,
        .fab {
          color: #e8eaec;
          font-size: 2em;
        }
      }
      & > .content {
        margin-left: 10px;
        .title {
          font-weight: 500;
          font-family: roboto;
          font-size: 15px;
        }

        button {
          margin-top: 5px;
          width: 100px;
        }
      }

      &.focus {
        background: #fff;
        border-left: 3px solid #5891d4;
        button {
          display: none;
        }
      }
    }
  }
}
</style>



