<script>
import { Methods } from "vue-handler-panels";
import slugify from "slugify";

export default {
  props: {
    name: {
      default() {
        return "add-property-modal";
      }
    }
  },
  data() {
    return {
      title: ""
    };
  },
  mounted() {
    $(this.$el)
      .find("input[name=property-title]")
      .focus();
  },
  methods: {
    check() {
      this.keyword;
    },
    submit() {
      this.$emit("submit", { title: this.title });
      this.title = "";
    }
  },
  computed: {
    canSubmit() {
      return this.title;
    }
    // ...mapState({
    //   user: state => state.auth.user,
    //   app: state => state.auth.application
    // })
  },
  components: {
    //
  },
  watch: {
    //
  }
};
</script>

<template>
  <modal :name="name">
    <div class="panel">
      <div class="panel-heading">Adicionar novo imvel</div>
      <div class="panel-body">
        <form v-on:submit.prevent="submit">
          <div class="content">
            <div class="form-group row">
              <div class="col-sm-12">
                <label for>Ttulo</label>
                <input name="property-title" class="form-control" v-model="title">
              </div>
            </div>
            <!-- <div class="form-group">
            <label for>Tipo</label>
            <select class="form-control" v-model="type">
              <option v-for="(row, key) in types" :key="key" :value="key">{{row.label}}</option>
            </select>
            </div>-->
          </div>
          <div class="bottom">
            <button class="btn btn-primary" :disabled="!canSubmit">
              <i class="fas fa-plus"></i>&nbsp; Adicionar
            </button>
          </div>
        </form>
      </div>
    </div>
  </modal>
</template>

<style lang="scss" scoped>
</style>



