<script>
import { applicationService } from "@/services";
import { mapState, mapActions } from "vuex";
import { router } from "@/route/router";

export default {
  data() {
    return {
      applications: [],
      authenticating: false,
      loading: true,
      no_apps: false
    };
  },
  created() {
    this.getAllApplications();
  },
  methods: {
    getAllApplications() {
      this.loading = true;
      applicationService
        .getAll()
        .finally(x => {
          this.loading = false;
        })
        .then(data => {
          this.applications = data.data;
          if (this.applications.length == 0) {
            this.no_apps = true;
          }
        });
    },
    ...mapActions({
      authApplicationByID: "root/authApplicationByID"
    }),
    authApp(id) {
      this.authenticating = true;
      this.authApplicationByID(id)
        .then(x => {
          router.push("home");
        })
        .catch(x => {
          alert("No foi possvel autenticar");
        })
        .finally(x => {
          this.authenticating = false;
        });
    }
  }
};
</script>
<template>
  <div class="page-wrapper">
    <div class="panel">
      <div class="panel-heading">
        <span class="title">Selecione uma aplicao para continuar</span>
      </div>
      <div class="panel-body">
        <div class="status" v-if="loading">Carregando aplicaes...</div>
        <div class="status" v-if="authenticating">Autenticando...</div>
        <div class="status" v-if="no_apps">Voc no possui acesso  nenhuma aplicao!</div>
        <ul class="listing" v-if="!loading && !authenticating">
          <li v-for="app in applications" :key="app.id">
            <a
              class="btn btn-default"
              :href="'/#/home?auth_application_id='+app.id"
              @click.prevent="authApp(app.id)"
            >{{app.name}}</a>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.page-wrapper {
  padding: 15px 30px;
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;

  .panel {
    width: 500px;
    font-family: roboto;

    .panel-heading {
      .title {
        font-weight: 500;
        font-size: 17px;
      }
    }

    .panel-body {
      ul.listing {
        display: flex;
        flex-direction: column;

        & > li {
          margin-bottom: 10px;

          a.btn {
            width: 100%;
            text-align: left;
            border-radius: 1px;
            font-size: 16px;
            font-weight: 500;
            font-family: roboto;
            color: #2a58c3;
            border-color: #e4e4e4;
          }
        }
      }
      .status {
        color: #667;
        font-weight: 500;
        font-size: 15px;
        font-weight: 500;
        font-family: roboto;
      }
    }
  }
}
</style>


