import { applicationService } from '../services';

const state = {
    all: {}
};

const actions = {
    getAll({ commit }) {
        commit('getAllRequest');

        return applicationService.getAll()
            .then(
            response => commit('getAllSuccess', response.data),
                error => commit('getAllFailure', error)
            );
    },

    delete({ commit }, id) {
        commit('deleteRequest', id);

        applicationService.delete(id)
            .then(
                application => commit('deleteSuccess', id),
                error => commit('deleteSuccess', { id, error: error.toString() })
            );
    }
};

const mutations = {
    getAllRequest(state) {
        state.all = { loading: true };
    },
    getAllSuccess(state, applications) {
        state.all = { items: applications };
    },
    getAllFailure(state, error) {
        state.all = { error };
    },
    deleteRequest(state, id) {
        // add 'deleting:true' application to application being deleted
        state.all.items = state.all.items.map(application =>
            application.id === id
                ? { ...application, deleting: true }
                : application
        )
    },
    deleteSuccess(state, id) {
        // remove deleted application from state
        state.all.items = state.all.items.filter(application => application.id !== id)
    },
    deleteFailure(state, { id, error }) {
        // remove 'deleting:true' application and add 'deleteError:[error]' application to application 
        state.all.items = state.items.map(application => {
            if (application.id === id) {
                // make copy of application without 'deleting:true' application
                const { deleting, ...applicationCopy } = application;
                // return copy of application with 'deleteError:[error]' application
                return { ...applicationCopy, deleteError: error };
            }

            return application;
        })
    }
};

export const applications = {
    namespaced: true,
    state,
    actions,
    mutations
};



