import { userService, applicationService } from '../services';
import { cutRouterData } from '@/helpers/RouterData';
import { router } from '@/route/router';
import { store } from '@/_store/index'
import config from '../config';

const state = {
    initializing: true,
    ready: false,
    params: {},
};

function authUserByAccessToken(access_token) {

    store.dispatch('auth/reset', {})

    userService.getUserByAccessToken(access_token)

        .then(response => {

            let user = response.user;

            store.dispatch('auth/authenticate', {
                access_token: access_token,
                user: {
                    id: user.id,
                    name: user.name,
                    email: user.email,
                },
            })
        })

        .finally(() => store.dispatch('root/completeBoot'))
}



// ===================================================================================

const actions = {

    initialize({ commit, dispatch, state }, vue) {

        // Verifica a existncia de parmetros passados por queryString, as remove e salva em "params"
        dispatch('submitRouteData', vue);

        let params = state.params;

        /**
         * Se existe um acces_token configurado para processar,
         *  resetado os dados de autenticao antigo incluindo dados de aplicao autenticada
         * e  obtido um usurio novo diretamente pela api 
         */
        if (params.access_token) {

            authUserByAccessToken(params.access_token)

            return;
        }

        store.dispatch('auth/syncLocalStorage', {})

        if (params.auth_application_id) {
            dispatch('authApplicationByID', params.auth_application_id).finally(_ => {
                dispatch('completeBoot')
            });
            return;
        }

        dispatch('completeBoot', vue)
    },

    authApplicationByID({ commit, dispatch, state }, application_id) {

        return applicationService.getById(application_id)

            .then(response => {

                let application = response.data;

                // console.log(response)

                store.dispatch('auth/authenticateApp', {
                    application: {
                        id: application.id,
                        name: application.name,
                        appname: application.appname,
                    },
                })
            })
    },

    completeBoot({ commit }) {

        var state = store.state;

        if (!state.auth.user) {
            var path = window.location.protocol + '//' + window.location.hostname + ':' + window.location.port + "/#/";
            path += '?access_token={{access-token}}';
            window.location.href = config.domain_accounts + '/login?goback=' + btoa(path);
            return;
        }

        commit('ready');
    },

    submitRouteData({ commit }, vue) {

        let params = cutRouterData(['access_token', 'auth_application_id'], vue.$route);

        commit('setInitialParams', params);
    }
};

const mutations = {
    ready(state) {
        state.ready = true;
        state.initializing = false;
    },
    setInitialParams(state, params) {
        state.params = params;
    },
    setAuthApplication(state, application) {
        state.auth.application = application
    }
};

export const root = {
    namespaced: true,
    state,
    actions,
    mutations
};



