import { store } from '@/_store/index'

export function authHeader() {
    // return authorization header with jwt token
    let token = store.state.auth.access_token;

    if (token) {
        return { 'Authorization': 'Bearer ' + token };
    } else {
        alert('invalid-token')
        return {};
    }
}


