<script>
import { Panel } from "vue-handler-panels";
import { Methods } from "vue-handler-panels";
import FilterHandler from "@/lib/filter-handler/FilterHandler";

import JsonEditor from "./Editor";
import AddPropertyComponent from "./AddProperty";

export default {
  config: {
    name: "default",
    panel: {
      title: "Listing",
      width: "450"
    },
    panels: {
      editor: JsonEditor
    }
  },
  data() {
    return {
      data: {},
      componentKey: "",
      search: "",
      filtered: [],
      selectedItemKey: "",
      panel: this.$options.config.panel,
      rootData: {}
    };
  },
  created() {
    //
    // SETTING FILTER HANDLER
    this.$filter = new FilterHandler({
      propsData: {
        parent: this,
        data: this.data,
        hide: [
          "title",
          "code",
          "type",
          "goal",
          "price",
          "description",
          "location"
        ]
      }
    });

    this.$filter.$on("filter", data => (this.filtered = data));

    // BEFORE READY REGISTERING OPTIONS PANEPS
    this.$on("before-ready", controller => {
      controller.registerPanels(this.$options.config.panels);
    });

    // ON CLOSE EVENTS
    // this.$on("next-close", $vm => {
    //   //
    //   if (this.selectedItemKey == $vm.data.key) {
    //     this.selectedItemKey = "";
    //   }

    //   this.$emit("save", this);
    // });
  },
  methods: {
    ...Methods,

    setData(data) {
      // console.log("LISTING / SET DATA");
      this.data = data; //JSON.parse(JSON.stringify(data));

      for (var key in this.data) {
        if (this.data[key] instanceof Object) {
          this.$set(this.data[key], "export", undefined);
        } else {
          delete this.data[key];
        }
      }

      // console.log("SCOPED", this.data);

      this.$filter.data = this.data;
    },
    getData() {
      return this.data;
    },
    select(key) {
      if (this.selectedItemKey == key) {
        this.$forceUpdate();
        return;
      }
      this.selectedItemKey = key;

      this.openEditor();
    },
    openEditor() {
      this.controller.openNext("editor", $vm => {
        $vm.data = this.getSelectedItem();
        $vm.rootData = this.rootData;
      });
    },
    getSelectedItem() {
      return this.data[this.selectedItemKey]
        ? this.data[this.selectedItemKey]
        : null;
    },
    addNew() {
      var key = prompt("Chave do atributo");

      if (!key) {
        return;
      }

      var label = prompt("Ttulo do atributo");
      var type = prompt("Tipo do atributo", "stuff");
      var category = prompt("Categoria do atributo", "build");
      var sector = prompt("Setor do atributo", "room");

      this.data[key] = {
        key,
        label,
        type: type,
        class: ["ct-" + category, "st-" + sector]
      };

      this.$emit("save", this);
    },
    removeAttr(attr) {
      if (confirm("Deseja deletar o atributo '" + attr.data.label + "'?")) {
        this.controller.next((next, $vm) => {
          if (this.selectedItemKey == attr.key) {
            next.close(true);
          }
        });
        this.$delete(this.data, attr.key);
      }
    },
    dblClick(key) {
      var row = this.getSelectedItem();

      if (row._export == undefined) {
        this.$set(row, "_export", true);
      } else {
        row._export = undefined;
      }

      this.$emit("dbl-click", key);

      this.$forceUpdate();
    },
    newProperty() {
      this.$modal.show("add-property-modal");
    },
    _AddProperty() {
      return {
        submitProperty: data => {
          this.$set(this.data, data.key, data.property);
          this.$forceUpdate();
          this.$modal.hide("add-property-modal");
        }
      };
    }
  },
  inherit: true,
  computed: {},
  components: {
    Panel,
    AddPropertyComponent
  },
  watch: {
    search(search) {
      this.$filter.query = search;
    },
    data: {
      handler() {
        this.$filter.filter();
      },
      deep: true
    }
  }
};
</script>
<template>
  <Panel title="Atributos do imvel" icon="fas fa-list-ul" :settings="panel">
    <modal name="add-property-modal">
      <add-property-component
        :properties="data"
        v-on:submit="_AddProperty().submitProperty(arguments[0])"
      />
    </modal>
    <div class="panel-search">
      <input type="text" class="form-control search" placeholder="Filtrar" v-model="search">
    </div>
    <div class="toolbar">
      <select v-model="$filter.type" class="input-type">
        <option value>&lt;Tipo&gt;</option>
        <option value="stuff">Elemento</option>
        <option value="area">rea</option>
        <option value="money">Monetrio</option>
        <option value="options">Opes</option>
        <option value="date">Data</option>
        <option value="text">Texto</option>
      </select>
      <select v-model="$filter.export" class="input-type ml-2">
        <option value>&lt;Visibilidade&gt;</option>
        <option value="visible">Visveis</option>
        <option value="hide">Ocultos</option>
      </select>
      <button class="btn btn-sm btn-default ml-2" @click="newProperty">Novo atributo</button>
    </div>
    <div class="panel-listing">
      <ul class="listing" v-if="filtered">
        <li
          v-for="(row, index) in filtered"
          :key="index"
          :class="{selected: selectedItemKey==row.key, 'export': row.data._export, 'custom': row.data.custom}"
        >
          <ul class="optionsbar">
            <!-- <li @click="removeAttr(row)">
              <i class="fas fa-pencil-alt"></i>
            </li>-->
            <li @click="removeAttr(row)">
              <i class="fas fa-times"></i>
            </li>
          </ul>
          <div class="content" @click="select(row.key)" @dblclick="dblClick(row.key)">
            <div class="title">
              <span v-if="row.data.custom">
                <i class="far fa-plus-square"></i>&nbsp;
                <!-- <i class="fas fa-bookmark"></i>&nbsp; -->
                <!-- <i class="fas fa-book"></i>&nbsp; -->
                <!-- <i class="fas fa-cube"></i>&nbsp; -->
                <!-- <i class="fab fa-diaspora"></i>&nbsp; -->
                <!-- <i class="far fa-flag"></i>&nbsp; -->
                <!-- <i class="fas fa-flag"></i>&nbsp; -->
                <!-- <i class="far fa-heart"></i>&nbsp; -->
                <!-- <i class="fas fa-neuter"></i>&nbsp; -->
                <!-- <i class="fas fa-quote-left"></i>&nbsp; -->
                <!-- <i class="fas fa-tag"></i>&nbsp; -->
              </span>
              {{row.data.label}}
            </div>
            <div class="info">{{row.key}}</div>
          </div>
        </li>
      </ul>
    </div>
  </Panel>
</template>

<style lang="scss" scoped>
.toolbar {
  display: flex;
  padding: 0 15px 10px;
  background: #f5f6f7;
  flex-shrink: 0;

  select.input-type {
    border: 1px solid #d7dce0;
    border-radius: 2px;
    padding: 4px;
  }
}
.panel-listing {
  display: flex;

  ul.listing {
    flex-shrink: 1;
    overflow-y: auto;
    flex-grow: 1;
    border-top: 1px solid #e2e2e2;

    & > li {
      position: relative;
      border-bottom: 1px solid #eee;
      cursor: pointer;
      user-select: none;

      &.export {
        background: #f5f6f7 !important;
        button {
          display: none;
        }
      }
      &.custom {
        background: #fffef5 !important;
      }
      & > .content {
        color: #444;
        font-family: roboto;
        // background: red;
        padding: 5px 10px;

        .title {
          font-weight: bold;
        }
        .info {
          color: #8c9498;
        }
      }

      & > .optionsbar {
        z-index: 1;
        position: absolute;
        right: 0;
        background: #eaebed;
        display: none;
        border-bottom: 1px solid #ddd;
        border-left: 1px solid #ddd;

        li {
          width: 20px;
          height: 20px;
          display: flex;
          align-items: center;
          justify-content: center;
          color: #789;
        }
      }

      &:hover {
        .optionsbar {
          display: flex;
        }
      }

      &.selected {
        border-left: 4px solid #5891d4;
      }
      &.selected.export {
        // background: #ecedee !important;
      }
    }
  }
}
</style>


