
export default class {

    constructor($parent, componentKey, fn) {

        this.uid = Math.ceil(Math.random() * 100000000000001);

        this.isValid = false;

        if (!$parent) {
            return;
        }
        if (!componentKey || $parent.registeredPanels[componentKey] == undefined) {
            throw 'component-not-found'
        }

        var component = $parent.registeredPanels[componentKey];

        // ----------------------------------------------------------

        this.isValid = true;

        this.ready = false;

        this.vm = null;

        this.$parent = $parent;

        this.componentKey = componentKey;

        this._component = component;

        this.fn = fn;
    }

    get $vm() {
        return this.vm;
    }

    get component() {
        return this._component;
    }

    setReady(vm) {

        this.vm = vm;

        vm.controller = this;

        this.ready = true;

        vm.$emit('before-ready', this)

        this.fn.apply(this.parent, [this.vm]);

        var prev = this.prev();

        if (prev) {
            prev.$vm.$emit('next-ready', vm)

            vm.$on('hook:beforeDestroy', x => {
                prev.$vm.$emit('next-close', vm)
            })
        }

        vm.$emit('ready', this)
    }

    next(fn) {
        var next = this.$parent.getNextByUID(this.uid);
        if (next) {
            if (typeof fn == 'function') {
                fn(next, next.$vm)
            }
            return next;
        }
    }

    prev() {
        var prev = this.$parent.getPrevByUID(this.uid);
        if (prev) {
            if (typeof fn == 'function') {
                fn(prev, prev.$vm)
            }
            return prev;
        }
    }

    openNext(componentKey, fn) {

        var next = this.$parent.getNextByUID(this.uid);
        if (next) {
            if (!next.close()) {
                return;
            }
        }
        return this.$parent.openNextPanel(this.path(componentKey), fn);
    }

    toggleNext(componentKey, fn) {

        var next = this.$parent.getNextByUID(this.uid);
        if (next) {
            if (!next.close()) {
                return;
            }
            if (next.componentKey == this.path(componentKey)) {
                return;
            }
        }
        return this.$parent.openNextPanel(this.path(componentKey), fn);
    }

    close(force) {
        force = force === true ? true : false;

        if (!this.$vm) {
            return;
        }

        if (!force && !this.closeConfirm()) {
            return;
        }
        var next = this.$parent.getNextByUID(this.uid);
        if (next) {
            if (!next.close(force)) {
                return;
            }
        }
        return this.$parent.closeByUID(this.uid);
    }

    closeConfirm() {
        if (this.$vm && this.$vm.closeConfirm != undefined) {
            return this.$vm.closeConfirm.apply(this.$vm);
        }
        return true;
    }

    // recycle() {

    //     if (this.beforeRecycle) {
    //         try {
    //             this.beforeRecycle()
    //         } catch (e) {
    //             return new Promise((resolve, reject) => {
    //                 reject()
    //             });
    //         }
    //     }

    //     Object.assign(this.$data, this.$options.data.apply(this))

    //     this.unsaved = false;
    //     this.recycled = true;

    //     return true;
    // }

    path(componentKey) {
        return this.componentKey + (componentKey ? '.' + componentKey : '')
    }

    registerPanel(componentKey, component) {
        this.$parent.registerPanel(this.path(componentKey), component);
    }

    registerPanels(listing) {
        for (var key in listing) {
            this.$parent.registerPanel(this.path(key), listing[key]);
        }
    }

    componentName() {
        return this.componentKey.split('.').pop()
    }
}



