import Vue from 'vue';
import _ from 'lodash'

var isObject = o => {
	return o instanceof Object && o.constructor.name == 'Object';
};

var isArray = a => Array.isArray(a);

function is_scalar(mixedVar) { // eslint-disable-line camelcase
	return (/boolean|number|string/).test(typeof mixedVar)
}

// -----------------------------------------------------------------------

const LOCK = '--lock';
const ALLOW = '--allow';
const HIDE = '--hide';
const SHOW = '--show';
const RM = '--rm';

export default class {



	constructor() {

		this.reserveds = [LOCK, ALLOW, HIDE, SHOW, RM];

		this.modifiers = {

			checkLockeds(row, key, { lock }) {
				if (lock && lock.indexOf(key) > -1) {
					return undefined;
				}
				return row;
			},

			checkAlloweds(row, key, { alloweds }) {
				if (alloweds && alloweds.indexOf(key) === -1) {
					return undefined;
				}
				return row;
			},

			checkAfter({ pattern, hide, show, lock }) {

				if (hide) {
					hide.forEach(key => {
						if (!lock || lock.indexOf(key) === -1) {
							delete pattern[key];
						}
					});
				}

				if (show) {
					for (var key in pattern) {
						if (show.indexOf(key) >= 0) {
							continue;
						}
						delete pattern[key];
					}
				}
			},
		}

		this.events = {
			pressets: [
				data => {
					if (data.pattern.hasOwnProperty(LOCK)) {
						if (data.pattern[LOCK] === true) {
							data.break = true;
						}

						if (isArray(data.pattern[LOCK])) {
							data.validations.push(this.modifiers.checkLockeds)
							data.lock = data.pattern[LOCK];
							delete data.pattern[LOCK];
						}
					}

					if (data.pattern.hasOwnProperty(ALLOW)) {
						if (isArray(data.pattern[ALLOW])) {
							data.validations.push(this.modifiers.checkAlloweds)
							data.alloweds = data.pattern[ALLOW];
							delete data.pattern[ALLOW];
						}
					}

					if (data.payload.hasOwnProperty(HIDE)) {
						data.hide = data.payload[HIDE];
						delete data.payload[HIDE];
					}

					if (data.payload.hasOwnProperty(SHOW)) {
						data.show = data.payload[SHOW];
						delete data.payload[SHOW];
					}
				}
			],
			last: [this.modifiers.checkAfter],
		}
	}

	__merge(pattern, payload) {

		if (!isObject(pattern) || !isObject(payload)) {
			return payload;
		}

		var data = {
			pattern,
			payload,
			validations: []
		};

		this.events.pressets.forEach(fn => fn(data));

		if (data.break) {
			return pattern;
		}


		for (var key in payload) {
			console.log(payload);

			var row = payload[key]

			if (data.validations.length) {
				data.validations.forEach(fn => {
					row = fn(row, key, data);
				})
			}

			if (data.break) { break; }

			if (row === undefined) { continue; }

			
			if (pattern.hasOwnProperty(key)) {
				pattern[key] = this.__merge(pattern[key], row);
			} else {
				pattern[key] = isObject(row) ? this.parse(row) : row;
			}
		}

		this.events.last.forEach(fn => fn(data));

		return pattern;
	}

	__parse(data) {

		var response = isObject(data) ? {} : [];

		for (var i in data) {
			if (data[i] instanceof Object) {
				if (this.reserveds.indexOf(i) == -1) {
					response[i] = this.__parse(data[i]);
					continue;
				}
			}

			response[i] = data[i];
		}

		return response;
	}

	__diff(pattern, payload) {

		if (!isObject(pattern) || !isObject(payload)) {
			return payload;
		}

		var diff = {}

		for (var key in payload) {

			if (key.startsWith('--')) {
				continue;
			}

			if (!pattern.hasOwnProperty(key)) {
				diff[key] = payload[key];
				continue;
			}

			if (pattern[key] === payload[key]) {
				continue;
			}

			if (isObject(pattern[key]) && isObject(payload[key])) {
				var c = this.__diff(pattern[key], payload[key]);
				if (c) {
					diff[key] = c;
				}
				continue;
			}

			if (isArray(pattern[key]) && isArray(payload[key])) {
				if (JSON.stringify(pattern[key]) == JSON.stringify(payload[key])) {
					continue;
				}
			}

			diff[key] = payload[key];
		}

		for (var key in pattern) {
			if (!payload.hasOwnProperty(key)) {
				if (key.startsWith('--')) {
					continue;
				}
				if (diff['--hide'] == undefined) {
					diff['--hide'] = [];
				}
				diff['--hide'].push(key)
				continue;
			}
		}

		return !_.isEmpty(diff) ? diff : undefined;
	}

	parse(data) {
		return this.__parse(JSON.parse(JSON.stringify(data)));
	}

	diff(pattern, payload) {

		pattern = JSON.parse(JSON.stringify(pattern));
		payload = JSON.parse(JSON.stringify(payload));

		var diff = this.__diff(pattern, payload);

		if (diff) {
			return diff;
		}

		return isObject(pattern) ? {} : [];
	}

	merge(pattern, payload) {
		pattern = JSON.parse(JSON.stringify(pattern));
		payload = JSON.parse(JSON.stringify(payload));
		return this.__merge(pattern, payload);
	}

	clone(data) {
		return _.merge({}, data);
	}
}


