<script>
import { mapState, mapActions } from "vuex";

export default {
  computed: {
    ...mapState({
      auth: state => state.auth,
      application: state => state.auth.application,
      applications: state => state.applications.all
    })
  },
  created() {
    this.getAllApps();
  },
  methods: {
    setAuthApi(id) {
      window.location.href = "/?auth_application_id=" + id;
    },
    ...mapActions("applications", {
      getAllApps: "getAll",
      deleteUser: "delete"
    })
  }
};
</script>

<template>
  <div id="page-home" class="content-wrapper">
    <div class="panel">
      <div class="panel-body">
        <h1 class="title">Minhas aplicaes:</h1>
        <em v-if="applications.loading">Loading applications...</em>
        <span v-if="applications.error" class="text-danger">ERROR: {{applications.error}}</span>
        <ul class="applications" v-if="applications.items">
          <li
            v-for="app in applications.items"
            :key="app.id"
            :class="{focus: app.id==application.id}"
          >
            <div class="img">
              <i class="fas fa-drafting-compass"></i>
              <!-- <i class="fas fa-camera-retro"></i> -->
              <!-- <i class="fas fa-chess-rook"></i> -->
              <!-- <i class="fas fa-cubes"></i> -->
              <img src alt>
            </div>
            <div class="content">
              <div class="title">{{app.name}}</div>
              <button class="btn btn-xs btn-default" @click="setAuthApi(app.id)">Gerenciar
                <i class="fas fa-cog"></i>
              </button>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
.content-wrapper {
  padding: 15px 30px;
}

.panel {
  max-width: 1000px;
  h1.title {
    margin-left: 0px;
    margin-top: 0px;
    color: #4e5052;
    font-size: 20px;
    font-weight: 500;
    font-family: roboto;
  }
}

#page-home {
  ul.applications {
    display: flex;
    flex-direction: column;
    border-top: 1px solid #e4e5e6;

    & > li {
      padding: 10px;

      border-bottom: 1px solid #e4e5e6;
      background: #fafafb;
      padding: 10px;
      display: flex;

      & > .img {
        height: 50px;
        width: 50px;
        background: #fff;
        border: 1px solid #eee;

        display: flex;
        justify-content: center;
        align-items: center;

        .fas,
        .fab {
          color: #e8eaec;
          font-size: 2em;
        }
      }
      & > .content {
        margin-left: 10px;
        .title {
          font-weight: 500;
          font-family: roboto;
          font-size: 15px;
        }

        button {
          margin-top: 5px;
          width: 100px;
        }
      }

      &.focus {
        background: #fff;
        border-left: 3px solid #5891d4;
        button {
          display: none;
        }
      }
    }
  }
}
</style>




