<script>
// import { Panel, Methods } from "vue-handler-panels";
import { Panel, Methods } from "@/vendor/vue-handler-panels";
import ListingPanel from "./Attributes/Listing";
import { mapState, mapActions } from "vuex";
import { propertyService, applicationService } from "@/services";
import LocationPanel from "./Location";
import PicturesPanel from "./Pictures/Pictures";
import _ from "lodash";
import AttrPack from "./AttrPack/AttrPack.js";
// import JsonVersioner from "json-versioner";
import JsonVersioner from "@/vendor/json-versioner";
import { Money } from "v-money";

var jsonFormat = require("json-format");

let JSONEditor = require("jsoneditor");

let $ = require("jquery");

function parseAttr(data) {
  var response = {};
  for (var i in data) {
    response[data[i].key] = data[i];
  }
  return response;
}

function download(filename, text) {
  //
  var element = document.createElement("a");

  element.setAttribute(
    "href",
    "data:text/plain;charset=utf-8," + encodeURIComponent(text)
  );
  element.setAttribute("download", filename);

  element.style.display = "none";
  document.body.appendChild(element);

  element.click();

  document.body.removeChild(element);
}

/// ---------------------------------------------------------------------------------

export default {
  data() {
    return {
      id: null,
      data: {
        properties: {},
        media: {}
      },
      pattern: {
        properties: {},
        media: {}
      },
      panel: {
        width: "460",
        icon: false
      },
      loading: true,
      aliasTypes: {
        code: {},
        title: {},
        type: {},
        goal: {},
        price: {},
        description: {}
      },
      alias: {},
      teste: "TESTE",
      storage: {},
      saving: false,
      title: "",
      money: {
        decimal: ",",
        thousands: ".",
        prefix: "R$ ",
        // suffix: " #",
        precision: 2,
        masked: false
      }
    };
  },
  created() {
    //
    this.$jsv = new JsonVersioner();

    this.$on("sync-data", () => {
      this.setDataFromStorage();
    });

    this.$on("ready", $ct => {
      //
      this.$ct = $ct;

      $ct.registerPanel("listing", ListingPanel);
      $ct.registerPanel("location", LocationPanel);
      $ct.registerPanel("pictures", PicturesPanel);

      this.syncData(true);
    });
  },
  watch: {
    data: {
      handler() {
        this.$nextTick(x => {
          this.syncAliasAttributes();
        });
      },
      immediate: true
      // deep: true
    }
  },
  methods: {
    ...Methods,

    setData(data) {},

    clearStorageData() {
      localStorage.removeItem("property-" + this.id);
      this.storage = null;
    },

    setDataFromStorage() {
      //
      this.pattern = this.storageGetPattern();

      var data = this.storageGetData(this.id);

      if (Array.isArray(data)) {
        data = {};
      }

      var entity = this.$jsv.merge(this.pattern, data);

      this.data = entity;
    },

    openAttributes() {
      //
      this.$ct.toggleNext("listing", $vm => {
        //
        $vm.setData(this.data.properties);

        this.$set($vm, "rootData", this.data);

        $vm.$on("dbl-click", key => {
          var row = $vm.getSelectedItem();
          $vm.controller.$vm.select(key);
        });
      });
    },

    openLocation() {
      this.controller.toggleNext("location", $vm => {
        $vm.data = this.data.properties.location;
        $vm.rootData = this.data;
      });
    },

    openPics() {
      this.controller.toggleNext("pictures", $vm => {
        $vm.data = this.data;
      });
    },

    storageGetPattern() {
      var pattern = localStorage.getItem("pattern");
      return JSON.parse(pattern);
    },

    storageGetData(id) {
      //
      var data = JSON.parse(localStorage.getItem("property-" + id));
      if (!data) {
        return;
      }
      this.storage = data;

      return data;
    },

    setLocalPattern(data) {
      localStorage.setItem("pattern", JSON.stringify(data));
    },

    setLocalPayload(id, data) {
      localStorage.setItem("property-" + id, JSON.stringify(data));
      this.storageGetData(id);
    },

    syncData() {
      //
      this.loading = true;

      return Promise.all([this.getPattern(), this.getPayloadID(this.id)]).then(
        values => {
          //
          this.loading = false;

          this.property = values[1].data.data;

          this.setLocalPattern(values[0].data);

          this.setLocalPayload(this.id, this.property);

          this.$emit("sync-data");
        }
      );
    },

    getPayloadID(id) {
      return propertyService.getById(id);
    },

    getPattern() {
      return applicationService.getAttrPack();
    },

    syncAliasAttributes() {
      this.$set(this, "alias", {});
      if (!this.data.properties) {
        return;
      }
      var keys = Object.keys(this.aliasTypes);
      for (var i in keys) {
        if (this.data.properties[keys[i]] != undefined) {
          this.$set(this.alias, keys[i], this.data.properties[keys[i]]);
        }
      }
    },

    downloadData() {
      var payload = this.$jsv.diff(this.pattern, this.data);
      download("imovel.json", jsonFormat(payload));
    },

    saveData() {
      //
      this.$forceUpdate();

      var payload = this.$jsv.diff(this.pattern, this.data);

      this.$emit("get-save-data", payload);

      this.saving = true;

      return propertyService
        .update(this.id, payload)
        .then(data => {
          this.$notify({
            group: "main",
            title: "Imvel salvo com sucesso",
            // text: "Hello user! This is a notification!"
          });
          this.saving = false;
        })
        .catch(x => {
          this.saving = false;
        });
    },

    saveToStorage() {
      this.setLocalPayload(this.id, {
        properties: this.$jsv.getData().properties
      });
    },

    editorInit(editor) {
      require("brace/mode/json");
      require("brace/theme/chrome");
      editor.setOptions({
        readOnly: true,
        highlightActiveLine: false,
        highlightGutterLine: false
      });
      editor.renderer.$cursorLayer.element.style.opacity = 0;
    }

    // closeConfirm() {
    //   alert("none");
    //   return false;
    // }
  },
  components: {
    Panel,
    editor: require("vue2-ace-editor"),
    Money
  },
  computed: {
    ...mapState({
      auth: state => state.auth,
      application: state => state.auth.application
    }),
    computedTitle() {
      return this.title;
    },
    dataStr() {
      return JSON.stringify(this.data, null, 4);
    },
    aliasCode: {
      set(val) {
        if (this.alias.code) {
          if (!this.alias.code.data) {
            this.$set(this.alias.code, "data", {
              value: val
            });
          } else {
            this.$set(this.alias.code.data, "value", val);
          }
        }
      },
      get() {
        if (this.alias.code == undefined) {
          return undefined;
        }
        return _.get(this.alias.code, "data.value");
      }
    },
    aliasTitle: {
      set(val) {
        if (this.alias.title) {
          if (!this.alias.title.data) {
            this.$set(this.alias.title, "data", {
              value: val
            });
          } else {
            this.$set(this.alias.title.data, "value", val);
          }
        }
      },
      get() {
        if (this.alias.title == undefined) {
          return false;
        }
        return _.get(this.alias.title, "data.value");
      }
    },
    aliasPrice: {
      set(val) {
        if (this.alias.price) {
          if (!this.alias.price.data) {
            this.$set(this.alias.price, "data", {
              value: val
            });
          } else {
            this.$set(this.alias.price.data, "value", val);
          }
        }
      },
      get() {
        if (this.alias.price == undefined) {
          return false;
        }
        return _.get(this.alias.price, "data.value");
      }
    },
    aliasDescription: {
      set(val) {
        if (this.alias.description) {
          if (!this.alias.description.data) {
            this.$set(this.alias.description, "data", {
              value: val
            });
          } else {
            this.$set(this.alias.description.data, "value", val);
          }
        }
      },
      get() {
        if (this.alias.description == undefined) {
          return false;
        }
        return _.get(this.alias.description, "data.value");
      }
    },

    aliasType: {
      set(value) {
        if (this.alias.type) {
          if (!this.alias.type.data) {
            this.$set(this.alias.type, "data", {
              value: value
            });
          } else {
            this.$set(this.alias.type.data, "value", value);
          }
        }
      },
      get() {
        if (this.alias.type == undefined) {
          return false;
        }
        return _.get(this.alias.type, "data.value");
      }
    },
    aliasTypeOptions() {
      if (!this.alias.type) {
        return {};
      }

      if (typeof this.alias.type.options == "string") {
        return _.get(this.data, this.alias.type.options);
      } else {
        return this.alias.type.options;
      }
    },
    aliasGoal: {
      set(value) {
        if (this.alias.goal) {
          if (!this.alias.goal.data) {
            this.$set(this.alias.goal, "data", {
              value: value
            });
          } else {
            this.$set(this.alias.goal.data, "value", value);
          }
        }
      },
      get() {
        if (this.alias.goal == undefined) {
          return false;
        }
        return _.get(this.alias.goal, "data.value");
      }
    },
    aliasGoalOptions() {
      return this.alias.goal ? this.alias.goal.options : {};
    }
  },
  directives: {
    focus: {
      inserted: function(el) {
        //
      }
    }
  }
};
</script>

<template>
  <Panel id="property-editor-panel" :title="computedTitle" :settings="panel">
    
    <div class="loading" v-if="loading">Carregando...</div>
    <div class="panel-body" v-if="!loading">
      <div class="left">
        <!-- <div class="toolbar">
          <button class="btn btn-sm btn-default" @click="clearStorageData">Clear storage</button>
          <button class="btn btn-sm btn-default ml-2" @click="syncData(true)">
            <i class="fas fa-sync"></i>
          </button>
          <div class="grow"></div>
        </div>-->
        <div class="form-group">
          <span>Cdigo</span>
          <input class="form-control" v-model="aliasCode" v-focus :disabled="aliasCode===false">
        </div>
        <div class="form-group">
          <span>Tipo</span>
          <select class="form-control" v-model="aliasType" :disabled="aliasType===false">
            <option v-for="(option, i) in aliasTypeOptions" :key="i" :value="i">{{option.label}}</option>
          </select>
        </div>
        <div class="form-group">
          <span>Objetivo</span>
          <select class="form-control" v-model="aliasGoal" :disabled="aliasGoal===false">
            <option v-for="(option, i) in aliasGoalOptions" :key="i" :value="i">{{option.label}}</option>
          </select>
        </div>
        <div class="form-group">
          <span>Ttulo</span>
          <input class="form-control" v-model="aliasTitle" :disabled="aliasTitle===false">
        </div>
        <div class="form-group">
          <span>Valor</span>
          <!-- <input class="form-control" v-model="aliasPrice" :disabled="aliasPrice===false"> -->
          <money
            class="form-control"
            v-model="aliasPrice"
            v-bind="money"
            :disabled="aliasPrice===false"
          ></money>
        </div>
        <div class="form-group">
          <span>Descrio</span>
          <textarea
            class="form-control"
            v-model="aliasDescription"
            :disabled="aliasDescription===false"
            rows="10"
          />
        </div>

        <div class="form-group">
          <div class="buttons">
            <button class="btn btn-default" @click="openLocation">
              <i class="fas fa-map-marked-alt"></i> &nbsp; Localizao
            </button>
            <button class="btn btn-default" @click="openAttributes">
              <i class="fas fa-list"></i> &nbsp; Atributos
            </button>
            <button class="btn btn-default" @click="openPics">
              <i class="far fa-images"></i> &nbsp; Fotos
            </button>
          </div>
        </div>

        <!-- <br>
        <button class="btn btn-default" @click="saveToStorage">Save to Storage</button>-->
      </div>
      <!-- <div class="right" style="display:none">
        <div id="jsoneditor" style="margin-top:0"></div>
        <editor class="mb-2" :value="dataStr" @init="editorInit" lang="json" theme="chrome"></editor>
      </div>-->
    </div>
    <div class="panel-footer" v-if="!loading">
      <button class="btn btn-default mr-3" @click="downloadData" :disabled="saving">
        <i class="fas fa-download"></i>
      </button>
      <button class="btn btn-primary w-100" @click="saveData" :disabled="saving">
        <span v-if="saving">Salvando...</span>
        <span v-if="!saving">
          <i class="fas fa-save"></i> &nbsp;Salvar alteraes
        </span>
      </button>
    </div>
  </Panel>
</template>

<style lang="scss">
#property-editor-panel {
  align-items: flex-start;
}
</style>
  
<style lang="scss" scoped>
.btn-primary.disabled,
.btn-primary:disabled {
  color: #fff;
  background-color: #3097d1;
  border-color: #2a88bd;
}
.panel {
  flex-shrink: 0;
  .loading {
    font-weight: 500;
    padding: 15px;
    font-family: roboto;
    color: #525354;
    display: flex;
  }

  .panel-body {
    flex-direction: row !important;

    .left {
      display: flex;
      flex-direction: column;
      flex-grow: 1;

      .toolbar {
        display: flex;
        justify-content: flex-end;
      }
      .buttons {
        display: flex;
        margin-left: -5px;
        margin-right: -5px;
        margin-bottom: 10px;

        .btn {
          width: 100%;
          margin-left: 5px;
          margin-right: 5px;
        }
      }

      textarea.form-control {
        height: 100px;
      }
    }
    .right {
      width: 400px;
      display: flex;
      flex-direction: column;

      .ace_editor {
        border: 1px solid #ddd;
      }
    }
  }
}
</style>


