<script>
import { Methods } from "vue-handler-panels";
import slugify from "slugify";

export default {
  props: {
    properties: {
      default() {
        return {
          //
        };
      }
    }
  },
  data() {
    return {
      title: "",
      type: null,
      keyEditionEnabled: false,
      keyname: "",
      types: {
        stuff: { label: "Atributo padro" },
        text: { label: "Texto" },
        date: { label: "Data" },
        area: { label: "rea" }
        // money: { label: "Valor monetrio" },
        // options: { label: "Options" },
      }
    };
  },
  mounted() {
    $(this.$el)
      .find("input[name=property-title]")
      .focus();
  },
  methods: {
    ...Methods,
    setData(data) {
      this.data = data;
    },
    getData() {
      // console.log("editor get data");
      return this.data;
    },
    check() {
      this.keyword;
    },
    submit() {
      if (this.properties[this.keyname]) {
        alert("Atributo indisponvel");
        return;
      }

      var property = {
        custom: true,
        type: this.type,
        label: this.title,
        data: {}
      };

      this.$emit("submit", { key: this.keyname, property });
    }
  },
  computed: {
    computedKeyname: {
      get() {
        if (!this.keyEditionEnabled) {
          this.keyname = slugify(this.title).toLowerCase();
        }
        return this.keyname;
      },
      set(val) {
        this.keyname = slugify(val).toLowerCase();
        this.check();
      }
    },
    canSubmit() {
      if (this.properties[this.keyname]) {
        return false;
      }
      return this.title && this.keyname && this.type;
    }
    // ...mapState({
    //   user: state => state.auth.user,
    //   app: state => state.auth.application
    // })
  },
  components: {
    //
  },
  watch: {
    //
  }
};
</script>

<template>
  <div class="panel">
    <div class="panel-heading">Adicionar atributo</div>
    <div class="panel-body">
      <!-- <v-progress-linear :indeterminate="true"></v-progress-linear>
      <v-progress-circular indeterminate color="primary"></v-progress-circular>-->
      <div class="content">
        <div class="form-group row">
          <div class="col-sm-6">
            <label for>Ttulo</label>
            <input name="property-title" class="form-control" v-model="title">
          </div>
          <div class="col-sm-6">
            <label for>Palavra-chave</label>
            <span style="display:flex">
              <input class="form-control" v-model="computedKeyname" :disabled="!keyEditionEnabled">
              <b-btn
                v-b-tooltip.hover
                title="Modo automtico"
                class="btn btn-sm btn-default ml-2"
                @click="keyEditionEnabled=!keyEditionEnabled"
              >
                <i class="fas fa-lock"></i>
              </b-btn>
            </span>
          </div>
        </div>
        <div class="form-group">
          <label for>Tipo</label>
          <select class="form-control" v-model="type">
            <option v-for="(row, key) in types" :key="key" :value="key">{{row.label}}</option>
          </select>
        </div>
      </div>
      <div class="bottom">
        <button class="btn btn-primary" @click="submit" :disabled="!canSubmit">
          <i class="fas fa-plus"></i>&nbsp; Adicionar
        </button>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
</style>



