<script>
import { Panel } from "vue-handler-panels";
import { Methods } from "vue-handler-panels";
import StuffModule from "./Modules/Stuff";
import OptionsModule from "./Modules/Options";
import AreaModule from "./Modules/Area";
import LocationModule from "./Modules/Location";
import TextModule from "./Modules/Text";
import MoneyModule from "./Modules/Money";
import DateModule from "./Modules/Date";

export default {
  data() {
    return {
      data: {},
      panel: {
        width: "400"
      },
      dirty: false,
      rootData: {}
    };
  },
  methods: {
    ...Methods,
    setData(data) {
      this.data = data;
    },
    getData() {
      // console.log("editor get data");
      return this.data;
    },
    toggleExport() {
      if (this.data._export == undefined) {
        this.$set(this.data, "_export", true);
      } else {
        this.data._export = undefined;
      }
    },
    btnClose() {
      this.close();
    }
  },
  computed: {
    title() {
      return this.data.label;
    },
    currentModule() {
      return this.$options.modules[this.data.type];
    },
    computedExport() {
      return this.data._export;
    }
  },
  modules: {
    stuff: StuffModule,
    options: OptionsModule,
    area: AreaModule,
    location: LocationModule,
    text: TextModule,
    money: MoneyModule,
    date: DateModule
  },
  components: {
    Panel
  },
  watch: {},
  created() {
    Object.assign(this.$data, this.$options.data());
    Object.assign(this.$data, this.settings);
  }
};
</script>

<template>
  <div class="panel-component">
    <div class="panel" :class="{privated:!computedExport}">
      <!-- <div class="panel-heading">
        <span class="title">{{title}}</span>
      </div>-->
      <div class="panel-heading">
        <!-- <i class="far fa-edit"></i> -->
        <div class="title">{{title}}</div>
        <button
          class="btn btn-sm btn-export"
          @click="toggleExport"
          :class="{active:computedExport}"
        >
          <span v-if="computedExport">
            <i class="fas fa-check"></i> &nbsp;Exportar
          </span>
          <span v-if="!computedExport">
            <i class="fas fa-minus mr-1"></i> &nbsp;Desativado
          </span>
        </button>
        <button class="btn btn-sm btn-default" @click="btnClose()">
          <i class="fas fa-times"></i>
        </button>
      </div>
      <div class="panel-body">
        <Module :is="currentModule" :params="data" :rootData="rootData"/>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
// .panel-component {
//   .panel.privated {
//     .panel-body {
//       .form-control {
//         // background: #f5f6f7 !important;
//         // border: 3px solid red;
//       }
//     }
//   }
// }
</style>
<style lang="scss" scoped>
.panel-component {
  flex-direction: column;

  .panel {
    width: 360px !important;
    box-shadow: 0px 0px 0px 2px #80abde;
    border: none;

    &.privated {
      box-shadow: none;
      outline: 1px solid #ddd;
      background: #f9fafb;
    }
    .panel-heading {
      .title {
        margin-left: 0px;
      }
      .btn {
        display: flex;
        padding: 0px 10px;
      }
      .btn-export {
        margin-right: 6px;
        font-size: 13px;
        outline: none;
        box-shadow: none;
        background: #fff;
        border: 1px solid #ddd;

        i {
          font-size: 13px;
        }

        &.active {
          border: 1px solid #eee;
          font-weight: 500;
        }
      }
    }
  }
}
</style>



