<script>
export default {
  props: {
    title: {
      type: String,
      default: "[Untitled]"
    },
    icon: {
      type: String,
      default: "fas fa-columns"
    },
    settings: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      width: "300"
    };
  },
  created() {
    Object.assign(this.$data, this.$options.data());
    Object.assign(this.$data, this.settings);
  },
  watch: {
    settings: {
      handler(settings) {
        Object.assign(this.$data, this.$options.data());
        Object.assign(this.$data, settings);
      },
      deep: true
    }
  },
  methods: {
    btnClose() {
      this.$parent.close();
    }
  },
  computed: {
    style() {
      return {
        width: this.width + "px"
      };
    }
  }
};
</script>
<template>
  <div class="panel-component">
    <div class="panel" :style="style">
      <!-- <div class="panel-heading">
        <span class="title">{{title}}</span>
      </div>-->
      <div class="panel-heading">
        <i :class="icon"></i>
        <div class="title">{{title}}</div>
        <!-- <button class="btn btn-sm btn-default" @click="newAttribute()">
          Novo &nbsp;
          <i class="fas fa-plus"></i>
        </button>-->
        <button class="btn btn-sm btn-default ml-2" @click="btnClose()">
          <i class="fas fa-times"></i>
        </button>
      </div>

      <slot/>
    </div>
  </div>
</template>


