<script>
import { Panel } from "vue-handler-panels";
import { Methods } from "vue-handler-panels";
import FilterHandler from "@/lib/filter-handler/FilterHandler";
import { mapState, mapActions } from "vuex";
import { propertyService } from "@/services";
import slugify from "slugify";

import MainPanel from "./Main";
import AddPropertyModal from "./Modals/AddPropertyModal";

export default {
  data() {
    return {
      data: {},
      componentKey: "",
      search: "",
      filtered: [],
      selectedPropertyID: "",
      panel: {
        title: "Listing",
        width: "500"
      },
      rootData: {},
      loading: true,
      properties: {}
    };
  },
  computed: {
    ...mapState({
      auth: state => state.auth,
      application: state => state.auth.application
    })
  },
  created() {
    if (!this.application) {
      window.location.href = "/#/user/applications";
    }
    //
    // SETTING FILTER HANDLER
    this.$filter = new FilterHandler({
      propsData: {
        parent: this,
        data: {
          234: {
            label: "teste"
          }
        },
        indexRow(row) {
          return {
            label: row.title
              ? slugify(row.title)
                  .toLowerCase()
                  .replace("-", " ")
              : ""
          };
        }
      }
    });

    // this.$filter.addFilter(filtered => {
    //   return filtered.filter(el => {
    //     return el.data.type == this.type;
    //   });
    // });

    this.$filter.$on("filter", data => {
      this.filtered = data;
    });

    // BEFORE READY REGISTERING OPTIONS PANEPS
    this.$on("before-ready", controller => {
      controller.registerPanel("editor", MainPanel);
    });

    this.sync();
  },
  methods: {
    ...Methods,

    setData(data) {
      //
      this.data = data;

      for (var key in this.data) {
        if (this.data[key] instanceof Object) {
          this.$set(this.data[key], "export", undefined);
        } else {
          delete this.data[key];
        }
      }

      this.$filter.data = this.data;
    },
    getData() {
      return this.data;
    },
    select(id) {
      if (this.selectedPropertyID == id) {
        this.controller.next($vm => {
          this.selectedPropertyID = "";
          $vm.close();
        });
        return;
      }

      this.$nextTick(x => {
        this.selectedPropertyID = id;
        this.openEditor();
      });
    },
    openEditor() {
      this.controller.openNext("editor", $vm => {
        $vm.id = this.selectedPropertyID;
        var row = this.getSelectedItem();
        $vm.title = row.data.title;
        $vm.$on("hook:beforeDestroy", x => {
          if (this.selectedPropertyID == $vm.id) {
            this.selectedPropertyID = "";
          }
        });
      });
    },
    getSelectedItem() {
      for (var i in this.filtered) {
        if (this.filtered[i].key == this.selectedPropertyID) {
          return this.filtered[i];
        }
      }
    },
    addNewModal() {
      this.$modal.show("add-property");
      // this.$modal.show("dialog", {
      //   title: "Adicionar novo  Imvel",
      //   text: "Nome do Imvel",
      //   buttons: [
      //     {
      //       title: "OK",
      //       handler: () => {
      //         this.addNew("Criado por modal");
      //         this.$modal.hide("dialog");
      //       }
      //     },
      //     // {
      //     //   title: "", // Button title
      //     //   default: true, // Will be triggered by default if 'Enter' pressed.
      //     //   handler: () => {} // Button click handler
      //     // },
      //     {
      //       title: "Close"
      //     }
      //   ]
      // });
    },
    addNew(params) {
      var data = {
        properties: {
          title: {
            data: {
              value: params.title
            }
          }
        }
      };

      this.$notify({
        group: "main",
        type: "default",
        title: "Adicionando imvel..."
      });

      propertyService.insert(data).then(data => {
        this.$notify({
          group: "main",
          type: "success",
          title: "Imvel adicionado com sucesso"
          // text: "Hello user! This is a notification!"
        });
        this.sync();
      });
    },
    removeProperty(hash_id) {
      this.$modal.show("dialog", {
        title: "Deseja deletar este imvel?",
        // text: "Nome do Imvel",
        buttons: [
          {
            title: "Deletar",
            handler: () => {
              this.$notify({
                group: "main",
                type: "default",
                title: "Deletando imvel..."
                // text: "Hello user! This is a notification!"
              });
              propertyService
                .delete(hash_id)
                .then(x => {
                  this.$notify({
                    group: "main",
                    title: "Imvel deletado com sucesso"
                    // text: "Hello user! This is a notification!"
                  });
                })
                .catch(x => {
                  this.$notify({
                    group: "main",
                    title: "No foi possvel deletar o imvel",
                    type: "error",
                    text: "Se o problema persistir contacte-nos!"
                  });
                });
              this.$modal.hide("dialog");
            }
          },
          {
            title: "Cancelar"
          }
        ]
      });
    },
    sync() {
      this.loading = true;
      // this.controller.next($vm => {
      //   this.selectedPropertyID = "";
      //   $vm.close();
      // });
      propertyService.getAll().then(({ data }) => {
        this.loading = false;
        this.properties = data.data;
      });
    },
    getGoalLabel(goal) {
      switch (goal) {
        case "sale":
          return "Venda";
        case "rent":
          return "Locao";
        case "season":
          return "Temporada";
        case "model":
          return "Modelo";
      }
    },
    getMainPicURL(data) {
      if (data.media != undefined) {
        console.log("OK", data.media, data.config);

        for (var i in data.media.photos) {
          if (data.media.photos[i].main == true) {
            var id = data.media.photos[i].id;
            var c = data.config.media.photos;
            var size = 240;
            return (
              c.service_url +
              `/gallery/${c.app_id}/${c.bucket}/${id}/${id}-${size}.jpg`
            );
          }
        }
      }
    },
    _AddPropertyModal() {
      return {
        submit: data => {
          this.$modal.hide("add-property");
          this.addNew(data);
        }
      };
    }
  },
  components: {
    Panel,
    AddPropertyModal
  },
  watch: {
    search(search) {
      this.$filter.query = search;
    },
    properties(data) {
      if (data) {
        var items = {};
        for (var i in data) {
          var row = data[i];
          items[row.id] = row;
        }
        this.$filter.data = items;
        this.$filter.index();
        this.$filter.filter();
      }
    }
  }
};
</script>

<template v-if="application">
  <div id="property-editor-panel" class="panel-component">
    <v-dialog/>

    <add-property-modal
      name="add-property"
      v-on:submit="_AddPropertyModal().submit(arguments[0])"
    >ok</add-property-modal>

    <div class="panel">
      <div class="panel-heading">
        <span class="title">Meus Imveis</span>
      </div>
      <div class="panel-search">
        <input type="text" class="form-control search" placeholder="Filtrar" v-model="search">
        <button @click="sync" class="btn btn-sm btn-default ml-3" :disabled="loading">
          <i class="fas fa-sync"></i>
        </button>
        <button @click="addNewModal" class="btn btn-sm btn-default ml-3">
          <span>Novo</span>&nbsp;
          <i class="fas fa-plus"></i>
        </button>
      </div>
      <div class="loading" v-if="loading">Carregando imoveis...</div>
      <div class="panel-listing" v-if="!loading">
        <ul class="listing" v-if="filtered">
          <li
            v-for="(row, index) in filtered"
            :key="index"
            :class="{selected: selectedPropertyID==row.key}"
          >
            <ul class="optionsbar">
              <li @click="removeProperty(row.key)">
                <i class="fas fa-times"></i>
              </li>
            </ul>
            <div class="content" @click="select(row.data.id)">
              <div class="thumb">
                <img :src="getMainPicURL(row.data.data)">
              </div>
              <div class="title">
                <span class="main">{{row.data.title ? row.data.title : 'Sem ttulo'}}</span>
                <span class="ref" v-if="row.data.code">&nbsp;- {{ row.data.code }}</span>
              </div>
              <!-- <div class="info">
                <span class="mr-3" v-if="row.data.goal">{{getGoalLabel(row.data.goal)}}</span>
                <span class="mr-3" v-if="row.data.type">Tipo {{row.data.type}}</span>
              </div>-->
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.loading {
  font-weight: 500;
  padding: 15px;
  font-family: roboto;
  color: #525354;
  display: flex;
}
.panel-component {
  .panel {
    width: 600px;
  }
}
.panel-listing {
  display: flex;

  ul.listing {
    flex-shrink: 1;
    overflow-y: auto;
    flex-grow: 1;
    border-top: 1px solid #e2e2e2;

    & > li {
      position: relative;
      border-bottom: 1px solid #eee;
      cursor: pointer;
      user-select: none;

      &.export {
        background: #f5f6f7 !important;
        button {
          display: none;
        }
      }
      & > .content {
        color: #444;
        font-family: roboto;
        padding: 10px 15px;
        display: flex;

        .thumb {
          margin-right: 15px;
          position: relative;
          background: url(https://pictureimobland.blob.core.windows.net/system/default.jpeg);
          background-size: cover;
          background-position: center;

          img {
            width: 180px;
            height: 100px;
            object-fit: cover;
          }
        }
        .title {
          flex-grow: 1;
          font-weight: 500;
          font-size: 15px;
          display: flex;
          .ref {
            color: #aaa;
          }
        }
      }

      & > .optionsbar {
        z-index: 1;
        position: absolute;
        right: 0;
        background: #eaebed;
        display: none;
        border-bottom: 1px solid #ddd;
        border-left: 1px solid #ddd;

        li {
          width: 20px;
          height: 20px;
          display: flex;
          align-items: center;
          justify-content: center;
          color: #789;
        }
      }

      &:hover {
        .optionsbar {
          display: flex;
        }
      }

      &.selected {
        border-left: 4px solid #5891d4;
      }
      // &.selected.export {
      //   background: #ecedee !important;
      // }
    }
  }
}
</style>


