import { userService } from '@/services';
import { router } from '@/route/router';
import { cutRouterData } from '@/helpers/RouterData';
import { store } from '@/_store/index'

const user = JSON.parse(localStorage.getItem('user'));

const state = {
    access_token: null,
    user: null,
    application: null,
};

const actions = {

    reset({ commit }) {
        localStorage.removeItem('auth')
        commit('reset')
    },

    authenticate({ commit }, { user, access_token }) {

        localStorage.setItem('auth', JSON.stringify({ user, access_token }))

        commit('authenticate', { user, access_token })
    },

    authenticateApp({ commit }, { application }) {

        // console.log(application)

        localStorage.setItem('auth_application', JSON.stringify({ application }))

        commit('authenticate-app', { application })
    },

    syncLocalStorage({ commit }, { user, access_token }) {

        let auth = JSON.parse(localStorage.getItem('auth'))

        if (auth) {

            commit('authenticate', auth)

            let auth_application = JSON.parse(localStorage.getItem('auth_application'))

            if (auth_application) {
                commit('authenticate-app', auth_application)
            }
        }
    },

    logout({ commit }) {
        userService.logout();
        commit('logout');
    },
};

const mutations = {
    reset(state) {
        state.status = {};
        state.user = null;
    },
    authenticate(state, { user, access_token }) {
        state.user = user;
        state.access_token = access_token;
    },
    'authenticate-app'(state, { application }) {
        state.application = application;
    },
};

export const auth = {
    namespaced: true,
    state,
    actions,
    mutations
};


