import config from '../config';
import { authHeader } from '../helpers';
import { store } from '@/_store/index'
import axios from 'axios'

export const propertyService = {
    insert,
    getAll,
    getById,
    update,
    delete: _delete
};

function insert(data) {

    const instance = axios.create({
        baseURL: config.apiUrl,
        headers: authHeader(),
    });

    var application_id = store.state.auth.application.id;

    return instance.post("/app/properties", {
        application_id,
        data
    })
}

function getAll() {

    var app_id = store.state.auth.application.id;

    const instance = axios.create({
        baseURL: config.apiUrl,
        headers: authHeader(),
        // timeout: 1000,
    });

    return instance.get(`${config.apiUrl}/app/properties?application_id=` + app_id, {
        headers: {
            'Access-Control-Allow-Origin': '*',
            ...authHeader()
        },
    });
}


function getById(id) {

    const instance = axios.create({
        baseURL: config.apiUrl,
        headers: authHeader(),
        // timeout: 1000,
    });

    var application_id = store.state.auth.application.id;

    return instance.get("/app/property", {
        params: {
            application_id,
            id,
        }
    })
}

function update(property_id, data) {

    const instance = axios.create({
        baseURL: config.apiUrl,
        headers: authHeader(),
        // timeout: 1000,
    });

    var application_id = store.state.auth.application.id;

    return instance.put("/app/properties", {
        application_id,
        property_id,
        data
    })
}

// prefixed function name with underscore because delete is a reserved word in javascript
function _delete(property_id) {
    const instance = axios.create({
        baseURL: config.apiUrl,
        headers: authHeader(),
    });

    var application_id = store.state.auth.application.id;

    return instance.delete("/app/property?id=" + property_id + '&application_id=' + application_id);
}

function handleResponse(response) {
    return response.text().then(text => {
        const data = text && JSON.parse(text);
        if (!response.ok) {
            if (response.status === 401) {
                // auto logout if 401 response returned from api
                logout();
                location.reload(true);
            }

            const error = (data && data.message) || response.statusText;
            return Promise.reject(error);
        }

        return data;
    });
}


