<script>
export default {
  props: {
    params: {
      type: Object,
      default() {
        return {
          options: {}
        };
      }
    },
    rootData: {
      type: Object
    }
  },
  data() {
    return {
      options: {
        teste: {
          label: "defult"
        }
      }
    };
  },
  created() {
    if (this.params.data == undefined) {
      this.$set(this.params, "data", {
        value: null
      });
    }

    if (typeof this.params.options == "string") {
      this.options = _.get(this.rootData, this.params.options);
    } else {
      this.options = this.params.options;
    }
  }
};
</script>

<template>
  <div class="attr-module type-options">
    <div class="form-group">
      <span>Opes</span>
      <select class="form-control options" v-model="params.data.value">
        <option v-for="(row, key) in this.options" :key="key" :value="key">{{row.label}}</option>
      </select>
    </div>
  </div>
</template>


