<script>
import $ from "jquery";

export default {
  props: {
    params: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      options: {
        quilometro: "Quilmetro quarado",
        metro: "Metro quarado",
        milha: "Milha quarada",
        jarda: "Jarda quarada",
        pe: "P quadrado",
        polegada: "Polegada quadrada",
        hectare: "Hectare",
        acre: "Acre"
      }
    };
  },
  watch: {
    "params.data.value"() {
      //
    }
  },
  created() {
    if (this.params.data == undefined || this.params.data instanceof Array) {
      this.$set(this.params, "data", {
        value:undefined,
        type:undefined,
      });
    }
  },
  mounted() {
    $(this.$el)
      .find(".input-value")
      .select();
  }
};
</script>

<template>
  <div class="attr-module type-options">
    <div class="form-group">
      <span>Metragem</span>
      <div class="form-area">
        <input type="text" class="form-control input-value" v-model="params.data.value">
        <select class="metric-type form-control ml-2" v-model="params.data.type">
          <option v-for="(row, key) in options" :key="key" :value="key">{{row}}</option>
        </select>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.form-area {
  display: flex;
  .input-value {
    width: 100px;
  }
  .metric-type {
    flex-grow: 1;
  }
}
</style>


