<script>
import { mapState, mapActions } from "vuex";

import Initializer from "./Initializer";
import MainPanel from "./MainPanel";

export default {
  name: "app",
  created() {
    this.initialize(this);
  },
  computed: {
    ...mapState({
      user: state => state.auth.user,
      initializing: state => (state.root.initializing == true ? true : false)
    }),
    currentApplication() {
      if (this.initializing) {
        return Initializer;
      }
      return MainPanel;
    }
  },
  methods: {
    ...mapActions({
      initialize: "root/initialize"
    })
  },
  watch: {
    $route(to, from) {}
  },
  components: {}
};

</script>

<template>
  <div id="app">
    <app :is="currentApplication"/>
  </div>
</template>

<style lang="scss">
@import "@/assets/sass/app.scss";

html {
  height: 100%;

  body {
    height: 100%;

    ul {
      list-style: none !important;
      margin: 0px;
      padding: 0px;
      display: inline;
    }
  }

  .noselect {
    -webkit-touch-callout: none;
    /* iOS Safari */
    -webkit-user-select: none;
    /* Safari */
    -khtml-user-select: none;
    /* Konqueror HTML */
    -moz-user-select: none;
    /* Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none;
    /* Non-prefixed version, currently supported by Chrome and Opera */
  }
}

#app {
  height: 100%;
}
</style>



