<script>
import $ from "jquery";

export default {
  props: {
    params: {
      type: Object
    },
    rootData: {}
  },
  data() {
    return {};
  },
  created() {
    if (this.params.data == undefined) {
      this.$set(this.params, "data", {});
    }
    this.params.countries = {
      br: {
        label: "Brasil"
      }
    };

    // this.$on('get-save-data', data => {
    //   data.states = undefined;
    // });
  },
  mounted() {
    $(this.$el)
      .find(".form-states")
      .focus();
  },
  computed: {
    computedStates() {
      return this.rootData.extra ? this.rootData.extra.location.states : {};
    },
    country: {
      get() {
        if (!this.params.data.country) {
          this.$set(this.params.data, "country", {});
        }
        return this.params.data.country.acronym;
      },
      set(key) {
        this.params.data.country.acronym = key;
      }
    },
    state: {
      get() {
        if (!this.params.data.state) {
          this.$set(this.params.data, "state", {});
        }
        return this.params.data.state.acronym;
      },
      set(key) {
        this.$set(this.params.data.state, "acronym", key);
      }
    },
    city: {
      get() {
        if (!this.params.data.city) {
          this.$set(this.params.data, "city", {});
        }
        return this.params.data.city.label;
      },
      set(key) {
        this.$set(this.params.data.city, "label", key);
      }
    },
    district: {
      get() {
        if (!this.params.data.district) {
          this.$set(this.params.data, "district", {
            label:''
          });
        }
        return this.params.data.district.label;
      },
      set(key) {
        this.$set(this.params.data.district, "label", key);
      }
    },
    address: {
      get() {
        if (!this.params.data.address) {
          this.$set(this.params.data, "address", {});
        }
        return this.params.data.address.label;
      },
      set(key) {
        this.$set(this.params.data.address, "label", key);
      }
    },
    observation: {
      get() {
        return this.params.data.observation
          ? this.params.data.observation
          : null;
      },
      set(key) {
        this.params.data.observation = key;
      }
    }
  }
};
</script>

<template>
  <div class="attr-module type-options">
    <div class="form-group">
      <span>Pas</span>
      <select class="form-control form-countries" v-model="country">
        <option v-for="(row, key) in params.countries" :key="key" :value="key">{{row.label}}</option>
      </select>
    </div>
    <div class="form-group">
      <span>Estado</span>
      <select class="form-control form-states" v-model="state">
        <option v-for="(row, key) in computedStates" :key="key" :value="key">{{row.label}}</option>
      </select>
    </div>
    <div class="form-group">
      <span>Cidade</span>
      <input class="form-control form-city" v-model="city">
    </div>
    <div class="form-group">
      <span>Bairro</span>
      <input class="form-control form-district" v-model="district">
    </div>
    <div class="form-group">
      <span>Endereo</span>
      <input class="form-control form-address" v-model="address">
    </div>
    <div class="form-group">
      <span>Observaes</span>
      <input class="form-control form-observation" v-model="observation">
    </div>
  </div>
</template>


