import { router } from '@/route/router';

export function cutRouterData(key, route) {
    
    let q = Object.assign({}, route.query);

    let response;

    if (Array.isArray(key)) {

        response = {}

        for (let i in key) {

            response[key[i]] = route.query[key[i]] ? route.query[key[i]] : null;

            delete q[key[i]];
        }

    } else {

        response = route.query[key];

        delete q[key];
    }

    router.replace({ query: q });

    return response;
}

export function getRouterData(key, router) {
    return router.query[key]
}


