import config from '../config';
import { authHeader } from '../helpers';
import axios from 'axios'
import { store } from '@/_store/index'

export const applicationService = {
    register,
    getAll,
    getById,
    update,
    delete: _delete,
    getAttrPack,
};

function getAttrPack() {
    
    const instance = axios.create({
        baseURL: config.apiUrl,
        headers: authHeader(),
        // timeout: 1000,
    });

    var application_id = store.state.auth.application.id;

    return instance.get("/app/property-attributes", {
        params: {
            application_id,
            v: 'latest',
        }
    })
}

function register(application) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(application)
    };

    return fetch(`${config.apiUrl}/applications/register`, requestOptions).then(handleResponse);
}

function getAll() {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${config.apiUrl}/applications`, requestOptions).then(handleResponse);
}


function getById(id) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${config.apiUrl}/application?id=${id}`, requestOptions).then(handleResponse);
}

function update(application) {
    const requestOptions = {
        method: 'PUT',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(application)
    };

    return fetch(`${config.apiUrl}/applications/${application.id}`, requestOptions).then(handleResponse);
}

function _delete(id) {
    const requestOptions = {
        method: 'DELETE',
        headers: authHeader()
    };

    return fetch(`${config.apiUrl}/applications/${id}`, requestOptions).then(handleResponse);
}

function handleResponse(response) {
    return response.text().then(text => {
        const data = text && JSON.parse(text);
        if (!response.ok) {
            if (response.status === 401) {
                // auto logout if 401 response returned from api
                logout();
                location.reload(true);
            }

            const error = (data && data.message) || response.statusText;
            return Promise.reject(error);
        }

        return data;
    });
}


