import { propertyService } from '../services';

const state = {
    all: {}
};

const actions = {
    
    getAll({ commit }) {
        commit('getAllRequest');

        return propertyService.getAll()
            .then(
                ({ data }) => commit('getAllSuccess', data),
                error => commit('getAllFailure', error)
            );
    },

    delete({ commit }, id) {
        commit('deleteRequest', id);

        propertyService.delete(id)
            .then(
                property => commit('deleteSuccess', id),
                error => commit('deleteSuccess', { id, error: error.toString() })
            );
    }
};

const mutations = {

    getAllRequest(state) {
        state.all = { loading: true };
    },
    getAllSuccess(state, properties) {
        state.all = { items: properties };
    },
    getAllFailure(state, error) {
        state.all = { error };
    },
    deleteRequest(state, id) {
        // add 'deleting:true' property to property being deleted
        state.all.items = state.all.items.map(property =>
            property.id === id
                ? { ...property, deleting: true }
                : property
        )
    },
    deleteSuccess(state, id) {
        // remove deleted property from state
        state.all.items = state.all.items.filter(property => property.id !== id)
    },
    deleteFailure(state, { id, error }) {
        // remove 'deleting:true' property and add 'deleteError:[error]' property to property 
        state.all.items = state.items.map(property => {
            if (property.id === id) {
                // make copy of property without 'deleting:true' property
                const { deleting, ...propertyCopy } = property;
                // return copy of property with 'deleteError:[error]' property
                return { ...propertyCopy, deleteError: error };
            }

            return property;
        })
    }
};

export const properties = {
    namespaced: true,
    state,
    actions,
    mutations
};



