<script>
import $ from "jquery";

export default {
  props: {
    params: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {};
  },
  watch: {
    "params.data.value"() {
      // alert("change");
    }
  },
  created() {
    if (this.params.data == undefined) {
      this.$set(this.params, "data", {
        value: null
      });
    }
  },
  mounted() {
    $(this.$el)
      .find(".input-value")
      .select();
  },
  computed: {
    label: {
      set(val) {
        _.set(this.params, "data.value", val);
      },
      get() {
        return this.params.data.value;
      }
    }
  }
};
</script>

<template>
  <div class="attr-module type-options">
    <div class="form-group">
      <span>Valor</span>
      <div class="form-area">
        <input type="text" class="form-control input-value" v-model="label">
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.form-area {
  display: flex;
  // .input-value {
  //   //
  // }
}
</style>


