<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "heading",
  computed: {
    ...mapState({
      user: state => state.auth.user,
      app: state => state.auth.application
    })
  },
  methods: {
    ...mapActions({})
  }
};
</script>

<template>
  <div class="component heading">
    <div class="content">
      <div class="main-search">
        <input
          class="main-search"
          name="q"
          type="text"
          placeholder="Pesquisar recursos, servios e ajuda"
          autocomplete="off"
        >
      </div>
    </div>
    <div v-if="user" class="menubar noselect">
      <router-link class="item" to="/user/applications">Aplicaes</router-link>
      <router-link class="item" style="margin-left:10px;" to="/login">Sair</router-link>
      <div class="item">
        <i class="fas fa-bell" style="font-size: 18px;"></i>
      </div>
      <div class="item">{{user.name}}</div>
    </div>
  </div>
</template>

<style lang="scss">
.component.heading {
  display: flex;
  height: 50px;
  background: #1d3561;
  flex-shrink: 0;

  .content {
    display: flex;
    flex-grow: 1;
    flex-direction: row;
    align-items: center;

    .main-search {
      input {
        width: 420px;
        margin-left: 20px;
        height: 34px;
        background: #3e5d90;
        border: 1px solid #102650;
        border-radius: 0px;
        padding-left: 10px;
        color: #fff;
        font-family: roboto;
        font-size: 14px;

        &::placeholder {
          color: #abbfd8;
        }
        &:focus {
          background: #4669a5 !important;
          outline: none; 
        }
      }
      a.btn {
        margin-left: 10px;
      }
    }
  }

  .menubar {
    display: flex;
    flex-grow: 0;
    flex-direction: row;
    margin-right: 20px;

    .item {
      display: flex;
      align-items: center;
      padding: 10px;
      cursor: pointer;
      color: #e2e7ef;

      &:hover {
        background: #244073;
      }
      &:active {
        background: #192e53;
      }
    }
  }
}
</style>



