import Vue from 'vue';
import slugify from 'slugify';
import _ from 'lodash'

export default Vue.extend({
  props: {
    data: {
      type: Object,
      default() {
        return {
          yokai: {
            label: " YOUKAY"
          }
        }
      }
    },
    filtered: {
      type: Array,
      default() {
        return [
          { key: "mononoke", data: { label: "NONONOKE" } }
        ]
      }
    },
    parent: {
      type: Object,
    },
    hide: {
      type: Array,
      default: () => []
    },
    indexRow: {
      type: Function,
      default: () => {
        //
      }
    }
  },
  data() {
    return {
      autoindex: true,
      autofilter: true,
      query: "",
      teste: {},
      indexed: [],
      type: '',
      export: null,
      filters: [],
    }
  },
  created() {
    // console.log("FILTER / CREATED")
    // console.log(this.filtered, this.data)
    // this.filtered[0].data.label = "OK"
  },
  watch: {
    filtered: {
      handler() {
        // console.log("FILTER / CHANGE FILTERED", this.filtered)
      },
      immediate: true
    },
    data: {
      handler(data) {
        // console.log("FILTER / CHANGE DATA", this.data)
        this.index();
        this.filter();
      },
      immediate: true
    },
    query() {
      if (this.autofilter) {
        this.filter()
      }
    },
    type() {
      if (this.autofilter) {
        this.filter()
      }
    },
    export() {
      if (this.autofilter) {
        this.filter()
      }
    },
  },
  methods: {

    getIndexedRow(key, data) {
      return {
        key,
        label: data.label != undefined ? slugify(data.label).toLowerCase() : "",
        data,
        ...(this.indexRow ? this.indexRow(data) : {})
      }
    },

    index() {

      var indexed = []

      var listing = JSON.parse(JSON.stringify(this.data))

      for (var key in listing) {
        indexed.push(this.getIndexedRow(key, this.data[key]));
      }

      // SOMENTE CHAVES VALIDAS
      this.indexed = indexed.filter((el) => {
        return this.hide.indexOf(el.key) == -1 && el.key.search(/^[A-Za-z0-9][A-Za-z0-9-\.]+$/i) > -1;
      });

      // console.log('FILTER / INDEX', this.indexed)
      this.$emit('index', this.indexed)
    },

    filter() {

      var filtered = this.indexed.filter(el => {
        return (el.key + " " + el.label).search(this.query) > -1;
      });

      if (this.type) {
        filtered = filtered.filter(el => {
          return el.data.type == this.type;
        });
      }

      if (this.export) {
        filtered = filtered.filter(el => {
          if (this.export == 'visible') {
            return el.data._export == true;
          }

          if (this.export == 'hide') {
            return !el.data._export;
          }
        });
      }

      for (var i in this.filters) {
        filtered = this.filters[i](filtered);
      }

      this.filtered = filtered;

      // console.log('FILTER / FILTER', this.filtered)

      this.$emit('filter', this.filtered)
    },

    getType() {
      return this.type;
    },

    setType(type) {
      return this.type = type;
    },

    addFilter(fn) {
      this.filters.push(fn);
    }
  }
});


