<script>
import { Methods } from "vue-handler-panels";
import LocationModule from "./Attributes/Modules/Location";

export default {
  data() {
    return {
      data: null,
      panel: {
        width: "400"
      },
      dirty: false,
      rootData: {}
    };
  },
  methods: {
    ...Methods,
    setData(data) {
      this.data = data;
    },
    getData() {
      // console.log("editor get data");
      return this.data;
    },
    btnClose() {
      this.close();
    }
  },
  created() {
    Object.assign(this.$data, this.$options.data());
    Object.assign(this.$data, this.settings);
  },
  components: {
    LocationModule
  }
};
</script>

<template>
  <div class="panel-component">
    <div class="panel" v-if="data">
      <div class="panel-heading">
        <i class="fas fa-map-marked-alt"></i>
        <div class="title">Editar localizao</div>
        <button class="btn btn-sm btn-default" @click="btnClose()">
          <i class="fas fa-times"></i>
        </button>
      </div>
      <div class="panel-body">
        <LocationModule :params="data" :rootData="rootData"/>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.panel-component {
  flex-direction: column;

  .panel {
    width: 360px !important;

    .panel-heading {
      .btn-export {
        margin-right: 6px;
        font-size: 13px;
        outline: none;
        box-shadow: none;
        background: #fff;
        border: 1px solid #ddd;

        i {
          font-size: 13px;
        }

        &.active {
          border: 1px solid #eee;
          font-weight: 500;
        }
      }
    }
  }
}
</style>



