<script>
import $ from "jquery";

export default {
  props: {
    params: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  methods: {
    toggleHas() {
      if (this.params.data.has == undefined || this.params.data.has == true) {
        this.$set(this.params.data, "has", false);
      } else {
        this.$set(this.params.data, "has", undefined);
      }
      this.$forceUpdate();
    }
  },
  data() {
    return {
      dirty: false
    };
  },
  computed: {
    has() {
      return this.params.data.has;
    }
  },
  created() {
    if (this.params.data == undefined) {
      this.$set(this.params, "data", {});
    }
    this.$parent.$watch("data._export", val => {
      if (!this.dirty && val) {
        this.params.data.has = true;
        this.dirty = true;
        this.$forceUpdate();
        this.$nextTick(x => {
          $(this.$el)
            .find(".input-value")
            .select();
        });
      }
    });
  },
  mounted() {
    $(this.$el)
      .find(".input-value")
      .select();
  }
};
</script>

<template>
  <div class="attr-module type-stuff">
    <div class="form-group">
      <span class="left">Quantidade</span>
      <input
        class="form-control input-value"
        v-model="params.data.value"
        :disabled="params.data.has==false"
      >
      <label class="right">
        <button
          class="btn btn-sm btn-check"
          @click="toggleHas"
          :class="{active:params.data.has!=false}"
        >
          <span v-if="params.data.has==true || params.data.has==undefined">
            <i class="fas fa-check mr-1"></i>
            Possui este requisito
          </span>
          <span v-if="params.data.has==false">No possui este requisito</span>
        </button>
      </label>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.attr-module {
  .fas.fa-check {
    color: #296fca;
  }
  button.btn-check {
    background: #f7f8f9;
    box-shadow: none;
    outline: none;
    border: 1px solid #eee;

    &.active {
      background: #fff;
      border: 1px solid #a1caea;
    }
  }
  input:disabled {
    border-color: #e6e7e8;
    background-color: #f9f9f9;
  }
  .input-value {
    width: 80px;
  }
  .form-group {
    display: flex;
    align-items: center;
    span.left {
      text-align: right;
      margin-right: 10px;
    }
    label {
      margin-bottom: 0px;
      padding-left: 10px;
    }
  }
}
</style>


