<script>
import { mapState, mapActions } from "vuex";

export default {
  computed: {
    ...mapState({
      auth: state => state.auth,
      application: state => state.auth.application,
      properties: state => state.properties.all
    })
  },
  created() {
    this.getAllApps();
  },
  methods: {
    setAuthApi(id) {
      window.location.href = "/#/?auth_application_id=" + id;
    },
    ...mapActions("properties", {
      getAllApps: "getAll",
      deleteUser: "delete"
    }),
    onClick(property) {
      this.$router.push("/#/property/edit?id=" + property.id);
    }
  }
};
</script>

<template>
  <div id="page-home" class="content-wrapper">
    <div class="panel">
      <div class="panel-heading">
        <h1 class="title">Meus Imveis</h1>
      </div>
      <div class="panel-body">
        <em v-if="properties.loading">Loading properties...</em>
        <span v-if="properties.error" class="text-danger">ERROR: {{properties.error}}</span>
        <ul class="properties" v-if="properties.items">
          <li v-for="property in properties.items" :key="property.id">
            <div class="img">
              <i class="fas fa-home"></i>
              <img src alt>
            </div>
            <div class="content" @click="onClick(property)">
              <div class="title">{{property.title}}</div>
              <div>{{property.code}}</div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
.content-wrapper {
  padding: 15px 30px;
  display: flex;
}

.panel {
  width: 700px;

  h1.title {
    margin-left: 0px;
    margin-top: 0px;
    color: #4e5052;
    font-size: 20px;
    font-weight: 500;
    font-family: roboto;
  }
}

#page-home {
  ul.properties {
    display: flex;
    flex-direction: column;
    border-top: 1px solid #e4e5e6;

    & > li {
      padding: 10px;

      border-bottom: 1px solid #e4e5e6;
      background: #fafafb;
      padding: 10px;
      display: flex;

      & > .img {
        height: 50px;
        width: 50px;
        background: #fff;
        border: 1px solid #eee;

        display: flex;
        justify-content: center;
        align-items: center;

        .fas,
        .fab {
          color: #e8eaec;
          font-size: 2em;
        }
      }
      & > .content {
        margin-left: 10px;
        .title {
          font-weight: 500;
          font-family: roboto;
          font-size: 15px;
        }

        button {
          margin-top: 5px;
          width: 100px;
        }
      }

      &.focus {
        background: #fff;
        border-left: 3px solid #5891d4;
        button {
          display: none;
        }
      }
    }
  }
}
</style>



