<script>
import Controller from "./Controller";

export default {
  data() {
    return {
      registeredPanels: {},
      openedPanels: {}
    };
  },
  mounted() {
    //
  },
  methods: {
    registerPanel(key, component) {
      this.registeredPanels[key] = component;
    },
    getPanelPositionByUID(uid) {
      var keys = Object.keys(this.openedPanels);

      for (var i in keys) {
        if (keys[i] == uid) {
          return i;
        }
      }
    },
    getPanelVmByUID(uid) {
      var i = this.getPanelPositionByUID(uid);
      return this.$children[i] != undefined ? this.$children[i] : null;
    },
    closeByUID(uid) {
      delete this.openedPanels[uid];
      this.$forceUpdate();
      return true;
    },
    openNextPanel(componentKey, fn) {
      //
      var controller = new Controller(this, componentKey, fn);

      this.openedPanels[controller.uid] = controller;

      this.$once("hook:updated", x => {
        controller.setReady(this.getPanelVmByUID(controller.uid));
      });

      this.$forceUpdate();
    },
    getNextByUID(uid) {
      var returnNext;
      for (var key in this.openedPanels) {
        if (returnNext) {
          return this.openedPanels[key];
        }
        if (key == uid) {
          returnNext = true;
        }
      }
    },
    getPrevByUID(uid) {
      var prevUID;
      for (var key in this.openedPanels) {
        if (key == uid) {
          return this.openedPanels[prevUID];
        }
        prevUID = key;
      }
    }
  },
  updated() {
    //
  }
};
</script>

<template>
  <div class="vue-handler-panels gap-2">
    <div v-for="(controller, i) in openedPanels" :key="i" :uid="i" :is="controller.component"/>
  </div>
</template>

<style lang="scss">
$gap1: 2.5px;
$gap2: 5px;
$gap3: 7.5px;

.vue-handler-panels {
  display: flex;

  & > .panel-component {
    display: flex;

    .panel {
      display: flex;
      margin-bottom: 0px;
      flex-direction: column;

      .panel-heading {
        border-bottom: 1px solid #eee;
        display: flex;
        flex-shrink: 0;
        align-items: center;
        height: 46px;

        .title {
          display: flex;
          flex-grow: 1;
          font-weight: 500;
          font-size: 17px;
          font-family: roboto;
        }
      }

      .panel-body {
        overflow: auto;
        display: flex;
        flex-direction: column;
        height: 100%;

        .form-control {
          height: 32px;
          border-radius: 2px;
          font-family: roboto;
          box-shadow: inset 0 1px 0px rgba(0, 0, 0, 0.05);
          padding: 6px 10px;
        }
      }

      .panel-footer {
        border-top: 1px solid #e5e7ea;
        display: flex;
        flex-shrink: 0;
        align-items: center;
      }

      & > .panel-search {
        padding: 9px 15px;
        flex-shrink: 0;
        background: #f5f6f7;
        display: flex;

        input.search {
          height: 32px;
          border-radius: 3px;
          font-family: roboto;
          box-shadow: none;
          border: 1px solid #d7dce0;
        }
      }
    }
  }
}
</style>


