<script>
import _ from "lodash";
import random from "random";
import { Drag, Drop } from "vue-drag-drop";
import axios from "axios";

function SquareCover(size, width, height) {
  //
  console.log(width, height);
  if (width > height) {
    return {
      height: size,
      width: Math.ceil((width / height) * size),
      horizontal: true
    };
  }
  return {
    width: size,
    height: Math.ceil((height / width) * size),
    horizontal: false
  };
}

function parseImage(file, data, fn) {
  //
  var reader = new FileReader();
  var img = document.createElement("img");
  var canvas = document.createElement("canvas");
  var context = canvas.getContext("2d");

  reader.onload = e => {
    //
    img.src = e.target.result;

    var size = data.size ? data.size : 200;

    canvas.width = size;
    canvas.height = size;

    setTimeout(xxx => {
      //
      var square = new SquareCover(size, img.width, img.height);

      if (square.horizontal) {
        var position_y = 0;
        var position_x = -((square.width - size) / 2);
      } else {
        var position_x = 0;
        var position_y = -((square.height - size) / 2);
      }

      //
      context.drawImage(
        img,

        0,
        0,
        img.width,
        img.height,

        position_x,
        position_y,

        square.width,
        square.height
      );

      setTimeout(xxx => {
        fn(canvas.toDataURL());
      }, 1);
    }, 1);
  };

  reader.readAsDataURL(file);
}

export default {
  props: {
    data: {
      type: Object
    },
    config: {
      type: Object
    }
  },
  data() {
    return {
      access_token:
        "eyJ0eXAiOiJKV1QiLCJhbGciOiJSUzI1NiIsImp0aSI6Ijk0Yzc2M2Y2ZWIxZTYyNGQ0ZTQzYjdmZDA2NTNlNWFiNDY5MDNjNjYxNjRjOWI2MGNhNDE2YjY3MDY4N2YxMDcyNDQ5MzU3ZmZlNTk1Nzg4In0.eyJhdWQiOiIxIiwianRpIjoiOTRjNzYzZjZlYjFlNjI0ZDRlNDNiN2ZkMDY1M2U1YWI0NjkwM2M2NjE2NGM5YjYwY2E0MTZiNjcwNjg3ZjEwNzI0NDkzNTdmZmU1OTU3ODgiLCJpYXQiOjE1NDQ5OTA3NDYsIm5iZiI6MTU0NDk5MDc0NiwiZXhwIjoxNTc2NTI2NzQ2LCJzdWIiOiIxIiwic2NvcGVzIjpbImNyZWF0ZSIsInJlYWQiLCJ1cGRhdGUiLCJkZWxldGUiXX0.KHclF8UPN2oQvdFRsxkhqGz9YFtWXx64zBHPhIxrg0J5gtZUguc612wRXanF7A4Hrvlqt3kPVwlCsG5UEwNY-ttZwD_smVotiMZ4k9xQoQjUwFm2EWfpDVBrwlUTzU-OwHq5-fco8eaFwdMIzcjEu1qe03pDEj48LxmBi1e3YSF9vnwl0s4-b1eLZGiKuF7K37pniqF9eKrtWj6IBldvpN6SKj2cnybuaD93uFnKRaUaWOpSkxs8fIU_TH0IFyFytid0ZfxlSNb2y_SsdbIBq645amYhq3gao2WxfRzCQGRY0q2Dqe3c8ztcBL0dvQWK8jUpHkhsLiT2ge_ElYnK7JzyIKt4m9mhJrh8I8G-vCMebmmK3mJTADAKOxKrQt785OpBlJ7rqYObAF8wHUnnkoyPcP6714xxMLJP6iCJNx6iBaEgsA7x9ZGxjfyfSaiglF6h9iFhwNHS224hR2Z1BmFwPhjQRazfUUUonDw3MxQInkjNda0lJznJeMlifSqvGpsvvU_mG6M6WNYcDtf2-HUkzIbOViDQUCE3S3xrS79f8zfXM1Wlxi74rO8eY8CTwQSlsKW-R1SLwGrJ0VHc1bIelh3-ENM70-UdcucPNlDNGkQhkTryFI-AEK6aOqYKQZF1yt0qm06zm42nfSpkq8M6CPyARkATNXQXXZoS__U",
      dragover: false,
      picdrag: false
    };
  },
  created() {
    //
    if (!this.data.media) {
      this.$set(this.data, "media", {});
    }

    if (!this.data.media.photos) {
      this.$set(this.data.media, "photos", []);
    }

    this.$on("file.uploaded", data => {
      //
      var response = data.result;

      if (response.success) {
        //
        var pic = response.picture;

        data.picture.inprocess = undefined;
        data.picture.thumb = undefined;
        data.picture.id = pic.id;
      } else {
        data.picture.inprocess = undefined;
        data.picture.error = true;
        this.$forceUpdate();
      }
    });

    this.$on("file.fail", data => {
      alert("fail :(");
      console.log("FAIL", data);
    });

    this.$on("file.progress", data => {
      data.picture.progress = (data.loaded / data.total) * 100;
      console.log(data.picture.progress);
    });

    this.$on("file.add", data => {
      //
      if (data.files[0].size > 1024 * 1024 * 10) {
        alert("A imagem enviada excede 10 megabytes de tamanho!");
        return;
      }

      var picture = {
        uid: random.int(0, 1000000000),
        progress: "0",
        inprocess: true,
        thumb: null
      };

      parseImage(data.files[0], { ratio: 140 }, bmp => (picture.thumb = bmp));

      // var reader = new FileReader();

      // reader.onload = e => {
      //   picture.thumb = e.target.result;
      // };

      // reader.readAsDataURL(data.files[0]);

      data.picture = picture;

      this.addPicture(picture);

      data.submit();
    });
  },
  mounted() {
    //
    $("#form-upload").fileupload({
      //
      // sequentialUploads: true,

      url: "https://pic-imobland.azurewebsites.net/api/app/pictures",

      dropZone: $("#pictures-dragzone"),

      beforeSend: (xhr, data) => {
        return xhr.setRequestHeader(
          "Authorization",
          "Bearer " + this.access_token
        );
      },

      formData: this.getFormData(),

      add: (e, data) => this.$emit("file.add", data),

      progress: (e, data) => this.$emit("file.progress", data),

      done: (e, data) => this.$emit("file.uploaded", data, e),

      fail: (e, data) => this.$emit("file.fail", data)
    });
  },
  methods: {
    getFormData() {
      return [
        { name: "bucket", value: this.data.config.media.photos.bucket },
        {
          name: "application_id",
          value: this.data.config.media.photos.app_id
        }
      ];
    },
    getSrc(pic) {
      var c = this.config;
      var size = 240;
      return (
        c.service_url +
        `/gallery/${c.app_id}/${c.bucket}/${pic.id}/${pic.id}-${size}.jpg`
      );
    },
    findFiles() {
      $("#form-upload")
        .find("input")
        .click();
    },

    clearErrors() {
      var pics = [];
      for (var i in this.data.media.photos) {
        var pic = this.data.media.photos[i];
        if (pic.id) {
          pics.push(pic);
        }
      }
      this.$set(this.data.media, "photos", pics);
    },

    cancelSendings() {},

    getPicByUID(uid) {
      for (var i in this.data.media.photos) {
        if (this.data.media.photos[i].uid == uid) {
          return this.data.media.photos[i];
        }
      }
    },

    movePicTo(uid, uid2) {
      if (uid == uid2) {
        return;
      }
      var main = null;

      var _pics = [];

      for (var i in this.data.media.photos) {
        var pic = this.data.media.photos[i];
        if (pic.uid == uid) {
          main = pic;
        } else {
          _pics.push(pic);
        }
      }

      if (!main) {
        return;
      }

      var pics = [];

      for (var i in _pics) {
        var pic = _pics[i];
        if (pic.uid == uid2) {
          pics.push(main);
          pics.push(pic);
        } else {
          pics.push(pic);
        }
      }

      if (!uid2) {
        pics.push(main);
      }

      this.$set(this.data.media, "photos", pics);
    },

    handlePicDrop(uid, data, event) {
      var el = this.getElByUID(uid);
      el.removeClass("drag-left");
      el.removeClass("drag-right");
      var el = this.getElByUID(data.uid);
      var self = this.getElByUID(uid);
      var pos = self.offset();
      if (event.pageX < pos.left + el.width() / 2) {
        this.movePicTo(data.uid, uid);
      } else {
        var uid2 = this.getNextPicUID(uid);
        this.movePicTo(data.uid, uid2);
      }
      event.preventDefault();
    },

    handleDrop(data, event) {
      this.dragover = false;
      event.preventDefault();
      const files = event.dataTransfer.files;
      const filenames = [];
      for (let i = 0; i < files.length; i++) {
        filenames.push(files.item(i).name);
      }
      ("");
    },
    addPicture(pic) {
      this.$set(this.data.media.photos, this.data.media.photos.length, pic);
    },

    picDragStart() {
      this.picdrag = true;
    },
    picDragEnd() {
      this.picdrag = false;
    },
    getElByUID(uid) {
      return $("li.gallery-square[uid=" + uid + "]");
    },
    getNextPicUID(uid) {
      var retornar = false;
      for (var i in this.pictures) {
        if (retornar) {
          return this.pictures[i].uid;
        }
        if (this.pictures[i].uid == uid) {
          retornar = true;
        }
      }
    },
    picDragLeave(uid, data, event) {
      var el = this.getElByUID(uid);
      el.removeClass("drag-left");
      el.removeClass("drag-right");
    },

    picDragOver(uid, data, event) {
      console.log("over");
      if (data.uid == uid) {
        return;
      }
      event.preventDefault();

      var el = this.getElByUID(data.uid);
      var self = this.getElByUID(uid);
      var pos = self.offset();
      if (event.pageX < pos.left + el.width() / 2) {
        self.removeClass("drag-right");
        self.addClass("drag-left");
      } else {
        self.removeClass("drag-left");
        self.addClass("drag-right");
      }
      // console.log(event.pageX);
      // console.log(pos.left, pos.left + el.width());
    },
    mainPicture(uid) {
      for (var i in this.data.media.photos) {
        this.data.media.photos[i].main = undefined;
      }
      if (uid) {
        var pic = this.getPicByUID(uid);
        this.$set(pic, "main", true);
      } else {
        if (this.data.media.photos.length) {
          this.$set(this.data.media.photos[0], "main", true);
        }
      }
      this.$forceUpdate();
    },

    removeAllPictures() {
      if (confirm("Esta ao remover todas as fotos")) {
        this.$set(this.data.media, "photos", []);
      }
    },
    removePicture(uid, event) {
      //
      var pic = this.getPicByUID(uid);

      if (this.data.media.photos.length) {
        this.data.media.photos.splice(this.data.media.photos.indexOf(pic), 1);
      } else {
        delete this.data.media.photos[pic];
      }

      this.mainPicture();

      return;

      // const instance = axios.create({
      //   baseURL: "http://pic-imobland.net/",
      //   headers: {
      //     Authorization:
      //       "Bearer " + //this.access_token
      //       "eyJ0eXAiOiJKV1QiLCJhbGciOiJSUzI1NiIsImp0aSI6IjFlYmIyNzg2MDBhN2YxMGRhZjU5MmM1YTA4OGRmN2VjZDUwZTJiMzUwYmUzNTNjNWRiODdlMTMzMmE5Njg5ZDM3ZGJhOTNiNDBhNWJlMTUwIn0.eyJhdWQiOiIxIiwianRpIjoiMWViYjI3ODYwMGE3ZjEwZGFmNTkyYzVhMDg4ZGY3ZWNkNTBlMmIzNTBiZTM1M2M1ZGI4N2UxMzMyYTk2ODlkMzdkYmE5M2I0MGE1YmUxNTAiLCJpYXQiOjE1NDUzNDUzMTYsIm5iZiI6MTU0NTM0NTMxNiwiZXhwIjoxNTc2ODgxMzE2LCJzdWIiOiIxIiwic2NvcGVzIjpbImNyZWF0ZSIsInJlYWQiLCJ1cGRhdGUiLCJkZWxldGUiXX0.I7vB5kGWkjqXuzzNI0xdoqop1PKJgJrfQ61cNV0LRmpfk9u312xKy2_XEkVbuRQ2K88U4IvrxddIsl2E41SSbMoSaS30j74_J0UvJ3rxoFslRpmEFqSR5h5FjC5NaVWODG60Ku6oFkpKPjezqwHur9IMNlVd1zk_sT6mPIXR2OHOWX1i_Wof0-jce-NXzdtTUcZfPpgzuaHWIqk15RvGaiXreXlWQ6EOAn53gY25BHv0ngH1hDYWuhMdJQ2MDr7x5PavdNsTHQCCgSCzNamxlbvxu3nyZfV8JyiqCiLuuxBkXpYztGcWMaYYvJBkzQxYetXT4LCRpM_Cy42UabjtomDR3ZkdpzZsrfG4JyrKirjohMKPn6I2F8vr6RAkgt9K1vjah6oADuvZnjgxIS3VRg_k7ui-1gCjmzBOGTkRu9NwTTfHuwF9yWXYK5OhaY6fSBxWoTQQeCVdLafhVWp6TK8VTQshdqXTiKhyx1eN8n0tVByzLg0bLPPpFQZGnucshiOVRclvnszwm1G1QLzsNFpfZQx9lgibn-Y3cqKPo0C3_hwI2Mtjj1jIMVEwWScLYXELJB65n0HOmrXjAAnnMdM9r0YZeYtdKkhKj1pcwgJNoSC2mJZQXF6s83U7wp7kcEjJfzCw2q_jq4rXpu3bzR-DW4QtTv4wHmTrAJLDv1o"
      //   }
      // });

      // var promisse = instance.delete("/api/app/pictures", {
      //   picture_id: pic.id
      // });

      // this.$set(pic, "deleting", true);

      // promisse
      //   .then(data => {
      //     this.data.media.photos.splice(this.data.media.photos.indexOf(pic), 1);
      //   })
      //   .catch(e => {
      //     alert("No foi possvel deletar a imagem selecionada");
      //   });
    },
    printData() {
      console.log(this.data.media.photos);
    },
    sending() {
      for (var i in this.data.media.photos) {
        if (this.data.media.photos[i].inprocess == true) {
          return true;
        }
      }
    }
  },
  components: {
    Drag,
    Drop
  },
  computed: {
    pictures() {
      var pics = [];
      for (var i in this.data.media.photos) {
        pics.push(this.data.media.photos[i]);
      }
      return pics;
    }
  }
};
</script>

<template>
  <div class="component-gallery">
    <form
      style="display:none"
      id="form-upload"
      method="post"
      action="/pic/property/"
      enctype="multipart/form-data"
    >
      <div id="drop">
        <input type="file" name="picture" accept="image/*" multiple>
      </div>
      <ul></ul>
    </form>
    <ul class="toolbar">
      <!-- <li @click="printData" class="link">Print Data</li> -->
      <li @click="findFiles" class="link">
        <i class="far fa-folder"></i> &nbsp; Procurar
      </li>
      <li class="link dropdown">
        <div class="dropdown-btn" data-toggle="dropdown">
          <i class="fas fa-ellipsis-v"></i>
        </div>
        <div class="dropdown-menu dropdown-menu-right">
          <!-- <li class="dropdown-item" @click="cancelSendings">Cancelar envios</li> -->
          <!-- <li class="dropdown-item" @click="clearErrors">Limpar no enviados</li> -->
          <li
            class="dropdown-item"
            :class="{'disabled':!pictures.length}"
            @click="pictures.length && removeAllPictures()"
          >Remover todas</li>
        </div>
      </li>
    </ul>
    <drop
      id="pictures-dragzone"
      class="gallery-listing"
      :class="{dragover}"
      @drop="handleDrop"
      @dragover="dragover=true"
      @dragleave="dragover=false"
    >
      <div class="dragarea"></div>
      <ul v-if="pictures.length>0">
        <li
          v-for="(pic, i) in pictures"
          :key="i"
          class="gallery-square"
          :class="{ 'inprocess':pic.inprocess, 'error':pic.error }"
          :uid="pic.uid"
        >
          <drop
            v-if="picdrag"
            class="pic-drop"
            @drop="handlePicDrop(pic.uid, ...arguments)"
            @dragover="picDragOver(pic.uid, ...arguments)"
            @dragleave="picDragLeave(pic.uid, ...arguments)"
          ></drop>
          <drag
            class="pic-drag gallery-pic"
            @dragstart="picDragStart"
            @dragend="picDragEnd"
            :transfer-data="{ uid:pic.uid }"
          >
            <div class="delete-message">Deletando...</div>
            <div v-if="pic.main" class="main-pic">Capa</div>
            <div class="options">
              <div class="dropdown">
                <div class="btn btn-sm btn-default" data-toggle="dropdown">
                  <i class="fas fa-caret-down"></i>
                </div>
                <div class="dropdown-menu dropdown-menu-right">
                  <a
                    class="dropdown-item"
                    v-if="!pic.main"
                    @click="mainPicture(pic.uid, ...arguments)"
                  >Foto de capa</a>
                  <a class="dropdown-item" @click="removePicture(pic.uid, ...arguments)">Exclur</a>
                </div>
              </div>
            </div>
            <div class="baston"></div>
            <div class="film"></div>
            <img v-if="pic.id" :src="getSrc(pic)">
            <img style="object-fit:cover" v-if="pic.thumb" :src="pic.thumb">
          </drag>
        </li>
      </ul>
      <div v-if="pictures.length==0">
        <img
          src="https://cdn.dribbble.com/users/1174720/screenshots/3640732/polaroid_2x.jpg"
          style="width:100%; opacity:0.3"
        >
      </div>
    </drop>
  </div>
</template>

<style lang="scss" scoped>
.component-gallery {
  display: flex;
  flex-direction: column;
  height: 100%;

  #canvas {
    background: #ddd;
  }
  .drop {
    background: #ddd;
    padding: 10px;
  }
  .gallery-listing {
    display: flex;
    overflow-y: scroll;
    position: relative;
    flex-grow: 1;

    .dragarea {
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0, 0, 0, 0.08);
    }
    ul {
      display: flex;
      flex-flow: row wrap;
      padding: 10px;
      align-content: flex-start;
      width: 100%;

      li.gallery-square {
        width: 140px;
        height: 150px;
        padding: 3px;
        position: relative;

        &.error {
          border: 2px solid red;
        }

        &.inprocess {
          img {
            opacity: 0.35;
          }
        }
        &:hover {
          .gallery-pic > .options {
            display: flex;
          }
        }
        &.drag-left {
          .gallery-pic > .baston {
            display: block;
            left: -4px;
          }
        }
        &.deleting {
          .gallery-pic {
            .delete-message {
              display: flex;
            }
            .options {
              display: none;
            }
            img {
              opacity: 0.3;
            }
          }
        }
        &.drag-right {
          .gallery-pic > .baston {
            display: block;
            right: -4px;
          }
        }

        .pic-drop {
          position: absolute;
          bottom: 0px;
          left: -4px;
          right: -4px;
          top: 0px;
          z-index: 101 !important;
        }
        .pic-drag {
          position: absolute;
          bottom: 0px;
          left: 0px;
          right: 0px;
          top: 0px;
          z-index: 100 !important;
        }
        .gallery-pic {
          position: relative;
          height: 100%;

          .delete-message {
            display: none;
            position: absolute;
            bottom: 55px;
            left: 0px;
            right: 0px;
            align-items: flex-end;
            justify-content: center;
            color: white;
            font-weight: 400;
            font-size: 15px;
            z-index: 1;
            font-family: roboto;
            padding: 2px;
            background: #3068bf;
          }
          .main-pic {
            position: absolute;
            top: 5px;
            left: 0px;
            align-items: flex-end;
            justify-content: center;
            color: white;
            font-weight: 400;
            font-size: 15px;
            font-family: roboto;
            padding: 0px 6px;
            background: #3068bf;
            i {
              font-size: 12px;
            }
          }
          .baston {
            display: none;
            position: absolute;
            height: 100%;
            border-left: 2px dashed #3782da;
            box-shadow: 0px 0px 0px 7px rgba(255, 255, 255, 0.6);
            width: 0px;
            z-index: 44;
          }
          .options {
            display: none;
            position: absolute;
            bottom: 0px;
            left: 0px;
            right: 0px;
            top: 0px;
            padding: 3px;
            color: #fff;
            z-index: 102 !important;
            justify-content: flex-end;

            .dropdown-menu {
              border-radius: 2px;
              margin: -1px 0;
            }
            .btn {
              display: flex;
              width: 22px;
              height: 22px;
              align-items: center;
              justify-content: center;
            }
          }

          .film {
            position: absolute;
            bottom: 0px;
            left: 0px;
            right: 0px;
            top: 0px;
            border: 1px solid rgba(0, 0, 0, 0.1);
            pointer-events: none;
            z-index: 1 !important;
          }

          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
      }
    }
  }
  ul.toolbar {
    padding: 0;
    background: #f5f6f7;
    flex-shrink: 0;
    display: flex;
    height: 40px;
    justify-content: flex-end;
    border-bottom: 1px solid #e1e5ea;

    & > li {
      display: flex;
      align-items: center;
      padding: 0 10px;
      font-weight: 400;
      font-family: roboto;
      font-size: 14px;
      cursor: pointer;

      &.link {
        &.dropdown {
          padding: 0px;

          .dropdown-btn {
            padding: 0 10px;
            height: 100%;
            display: flex;
            align-items: center;
            min-width: 34px;
            justify-content: center;
          }
          &:hover {
            display: flex;
          }
          .dropdown-menu {
            border-radius: 0;
            margin: 0;
          }
        }

        &:hover {
          background: #e6e8ea;
          color: #2f8fc5;
        }
      }
    }
  }
}
</style>


